.jupiterx-form-condition-manager {
    margin-top: 44px;

    .jupiterx-layout-condition-row {
        justify-content: flex-start;
        height: 67px;
        margin: 0px 0px 8px 35px;
        padding: 17px 37px 16px 10px;
        border-radius: 4px;
        background-color: #fafafa;

        /* Let the fourth column (the async select) consume remaining width */
        > div:nth-of-type(4) {
            flex: 1 1 auto;
            max-width: none;
            min-width: 0;
        }

        &:hover {
            background-color: #F5F6F6;
        }

        .mb-3 {
            padding-left: 0px !important;

            optgroup {
                text-transform: capitalize;
            }

            .fa-circle-plus {
                width: 16px;
                height: 16px;
                border: 1px solid #52c41a;
                color: #52c41a;
            }

            .fa-circle-minus {
                width: 16px;
                height: 16px;
                padding: 0;
                border: 1px solid #fd4b49;
                color: #fd4b49;
            }

            .parameter-icons {
                margin-right: 10px;
            }

            .fa-trash-can {
                color: #fd4b49;
            }

            select {
                min-width: 100%;
                margin: 0 15px 0 0;
                padding: 2px 12px;
                border: solid 1px #ced4da;
                border-radius: 4px;
                background-color: #ffffff;
                color: #000;
                font-size: 14px;
                font-style: normal;
                font-weight: normal;
                letter-spacing: normal;
                line-height: 1.14rem;
                text-align: left;
                box-shadow: none;
                font-stretch: normal;
                height: 100%;
            }

            .basic-single {

                .select__control {
                    height: 35px;
                    min-height: 35px !important;
                }

                div[class*="-control"] {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-content: space-between;
                    align-items: center;
                    height: 35px;
                    min-height: 35px !important;
                    box-shadow: none;

                    * {
                        border: none;
                        box-shadow: none;
                        appearance: none !important;
                    }

                    div:nth-child(1) {
                        /* Restore react-select v5 grid layout to overlay placeholder and input */
                        display: grid;
                        grid-template-areas: "value input";
                        grid-template-columns: 0 1fr;
                        position: relative;
                        align-items: center;
                        margin-top: -1px;

                        div {
                            /* Let the value container children expand fully within the grid */
                            width: 100%;
                            min-width: 0;
                            margin-top: 0;
                            column-gap: 0;
                            color: #000;
                            font-size: 14px;
                            font-style: normal;
                            font-weight: normal;
                            letter-spacing: normal;
                            line-height: 1.14rem;
                            text-align: left;
                            font-stretch: normal;

                            input {
                                width: 100% !important;
                            }
                        }

                        div[class*="placeholder"] {
                            position: absolute;
                            top: 50%;
                            left: 7px;
                            transform: translateY(-50%);
                            max-width: calc(100% - 14px);
                            padding-left: 0;
                            opacity: 0.5;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            pointer-events: none;
                        }

                        div[class*="singleValue"] {
                            padding-left: 7px;
                            cursor: text;
                        }
                    }

                    div:nth-child(2) {
                        margin-top: -2px;

                        .clearer-indicator {
                            position: absolute;
                            top: 4px;
                            right: 15px;
                            cursor: pointer;
                        }

                        span[class*="indicatorSeparator"] {
                            display: none;
                        }

                        svg {
                            margin-right: -5px;
                            color: #7c7c7c;
                        }

                    }

                    .jupiterx-layout-builder-condition-last-parameter-selected-item {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;
                        align-content: center;
                        align-items: center;
                        justify-content: flex-start;

                        a {
                            max-width: 92%;
                            text-decoration: underline;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            cursor: pointer;
                            overflow: hidden;
                            text-underline-offset: 3px;
                        }
                    }

                    .jupiterx-layout-builder-condition-last-parameter-dropdown-items {
                        display: none !important;
                    }
                }

                div[class*="-menu"] {
                    .jupiterx-layout-builder-condition-last-parameter-dropdown-items {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;
                        align-content: center;
                        align-items: center;
                        justify-content: flex-start;
                        column-gap: 3px;
    
                        span {
                            max-width: 92%;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            overflow: hidden;
                        }

                        a {
                            text-decoration: none;
                        }
                    }

                    .jupiterx-layout-builder-condition-last-parameter-selected-item {
                        display: none !important;
                    }

                    > div {
                        border: 1px solid #eee;
                        background-color: #FFFFFF;
                        z-index: 99999;
                    }
                }

                #react-select-2-input {
                    min-height: auto !important;
                }
            }

            .basic-single-first {

                .select__control {
                    height: 35px;
                    min-height: 35px !important;
                }

                div[class*="-control"] {
                    height: 35px;
                    min-height: 35px !important;

                    div:nth-child(1) {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: nowrap;
                        align-content: flex-start;
                        align-items: baseline;
                        justify-content: center;
                        margin-left: 2.5px;

                        div {
                            display: flex;
                            flex-direction: row;
                            align-items: flex-end;
                            justify-content: flex-start;
                            width: 100%;
                            column-gap: 7px;
                            color: #000;
                            font-size: 14px;
                            font-style: normal;
                            font-weight: normal;
                            letter-spacing: normal;
                            line-height: 1.14rem;
                            text-align: left;
                            font-stretch: normal;
                        }
                    }

                    div:nth-child(2) {
                        margin-top: -1px;

                        span[class*="indicatorSeparator"] {
                            display: none;
                        }

                        svg {
                            margin-right: -5px;
                            color: #7c7c7c;
                        }
                    }
                }
            }
        }

        .delete-row {
            position: relative;
            width: fit-content;
            padding-right: 0 ;

            button {
                position: absolute;
                margin-left: -1px;
                box-shadow: none;
            }
        }

        .jx-row-condition-conflict {
            padding: 10px 0 5px 0;
            color: red;
            text-align: center;
        }
    }

    .row-includes-conflict {
        height: auto !important;
    }

    .jupiterx-condition-submit-button {

        .add-new {
            height: 34px;
            margin: 5px 0px 0px 0px;
            padding: 5px 18px 9px 15px;
            border-radius: 4px;
        }

        .condition-saver {
            width: auto;
            padding-right: 37px;
            padding-left: 37px;
        }
    }
}

@media only screen and ( max-width: 992px) {
    .jupiterx-form-condition-manager {
        .jupiterx-layout-condition-row {
            height: auto !important;
        }
    }
}
