.templates-posts {
    .templates-header {
        margin-top: 30px !important;
        margin-lefT: 35px !important;

        .jx-layout-builder-header-left-side {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-content: center;
            align-items: center;
            justify-content: flex-start;
            padding: 0;
            column-gap: 30px;
        }

        .jx-layout-builder-header-right-side {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-content: center;
            align-items: center;
            justify-content: flex-end;
            column-gap: 10px;
        }

        .posts-header-title {
            color: #000;
            font-size: 20px;
            font-style: normal;
            font-weight: bold;
            letter-spacing: normal;
            line-height: 1.25;
            text-align: left;
            font-stretch: normal;
        }

        .posts-header-add {
            height: 34px !important;
            padding: 8px 18px 9px 13px;
            color: #ffffff;
            font-size: 14px;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.21;
            text-align: center;
            font-stretch: normal;
        }

        .post-types {
            display: flex;
            flex: auto;
            justify-content: flex-end;

            span {
                padding-top: 10px;
            }

            .post-type {
                min-width: 174px;
                height: 34px !important;
            }
        }

        .post-header-import {
            display: flex;
            right: 0px;
            flex-direction: row;
            flex-wrap: nowrap;
            align-content: center;
            align-items: center;
            justify-content: center;
            height: 34px !important;
            min-height: 34px !important;

            span {
                margin: 0 0 0 10px;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                letter-spacing: normal;
                line-height: 1.21;
                text-align: center;
                font-stretch: normal;
            }
        }

    }

    .layout-builder-import-form {
        margin: 28px 0px 13px 35px;

        .import-file-handler {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-content: center;
            align-items: center;
            justify-content: center;
            min-height: 400px;
            margin-top: 15px;
            border: solid 1px #e2e2e2;
            border-radius: 4px;
            background-color: #dfdfdf;

            .jx-drop-area {
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                align-content: center;
                align-items: center;
                justify-content: center;
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                row-gap: 22px;

                .layout-builder-upload-import {
                    width: 48px;
                    height: 48px;
                    color: #a7b5c2;
                }

                .jx-drop-import-text {
                    color: #000;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: normal;
                    letter-spacing: normal;
                    line-height: 1.2;
                    text-align: left;
                    font-stretch: normal;
                }

                .jx-drag-drop-text {
                    font-size: 14px;
                    font-style: normal;
                    font-weight: normal;
                    letter-spacing: normal;
                    line-height: 1.14;
                    text-align: left;
                    font-stretch: normal;
                }

                .jx-drop-drag-or {
                    color: #000;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: normal;
                    letter-spacing: normal;
                    line-height: 1.2;
                    text-align: left;
                    font-stretch: normal;
                }

                .file-container {
                    display: none;
                }

                .jx-layout-import-submit-btn {
                    display: none;
                }

                .jx-upload-file-wrapper {
                    padding: 8px 17px;
                    border-radius: 4px;
                    background-color: #000;
                    color: #fff;
                    cursor: pointer;
                }

                #jx-layout-import-file-upload-btn {
                    display: none;
                }

                #jx-layout-builder-import-error-area {
                    color: red;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: normal;
                    letter-spacing: normal;
                    line-height: 1.2;
                    text-align: left;
                    font-stretch: normal;
                }
            }

            .input-file {
                display: none;
            }
        }
    }

    .custom-col {
        width: 49% !important;
    }

    .items-container {
        display: grid;
        padding: 30px 0 8.5px 35px;
        grid-gap: 30px;

        .medium-height {
            .item-body {
                height: 50vh;
                overflow-x: hidden;

                iframe {
                    width: 100%;
                    min-height: 50vh !important;
                    pointer-events: none !important;
                }
            }
        }

        .large-height {
            .item-body {
                height: 50vh !important;
                overflow-x: hidden;

                iframe {
                    width: 100%;
                    height: 50vh !important;
                    pointer-events: none !important;
                }
            }
        }

        .layout-builder-templates-item {
            padding-right: 23px !important;
            padding-left: 23px !important;
            border: solid 1px #e2e2e2;
            border-radius: 4px;
            background-color: #ffffff;
            box-shadow: 0 7px 12px 0 rgba(0, 0, 0, 0.08) !important;

            .item-header {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                align-content: center;
                align-items: center;
                justify-content: space-between;
                padding-right: 0 !important;
                padding-left: 0 !important;

                .card-header-left-side {
                    display: flex;
                    flex-direction: row;
                    flex-grow: 1;
                    flex-wrap: wrap;
                    align-content: center;
                    align-items: center;
                    justify-content: flex-start;
                }

                .card-header-right-side {
                    display: flex;
                    flex-direction: row;
                    flex-grow: 1;
                    flex-wrap: wrap;
                    justify-content: flex-end;
                    column-gap: 14px;
                }

                .icon-circle {
                    width: 8px;
                    height: 8px;
                    margin-top: 2px;
                    margin-right: 10px !important;
                }

                .icon-circle-green {
                    color: #52c41a;
                }

                .icon-circle-grey {
                    color: #A7B5C2;
                }

                .dropdown-toggle {
                    box-shadow: none !important;
                }

                .layout-builder-title {
                    color: #000;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: normal;
                    letter-spacing: normal;
                    line-height: 1.13;
                    text-align: left;
                    font-stretch: normal;
                }

                .layout-builder-edit {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-content: center;
                    align-items: center;
                    justify-content: center;
                    margin: 5px 0 0 0;
                    padding: 5px 10px 5px 10px;
                    border-color: #000 !important;
                    color: #000 !important;
                    font-size: 14px;
                    font-weight: bold;
                    cursor: pointer;

                    .fa-edit {
                        width: 20px;
                        height: 18px;
                    }
                }

                .layout-builder-edit:hover {
                    background-color: #F2F2F2;
                    color: #000;
                }

                .layout-builder-edit:focus {
                    box-shadow: none !important;
                }

                .dropdown {
                    button {
                        width: 4px;
                        height: 20px;
                    }

                    button::after {
                        display:none;
                    }

                    .dropdown-menu {
                        min-width: 119px;
                        padding: 0 0 0 0;
                        border: solid 1px #e2e2e2;
                        border-radius: 4px;
                        background-color: #ffffff;
                        box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.16);
                        inset: 0px 50px 50px 0px;
                    }

                    .dropdown-item {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: nowrap;
                        align-content: center;
                        align-items: center;
                        justify-content: flex-start;
                        width: 119px !important;
                        height: 30px !important;
                        margin: 0px 0px;
                        padding: 0px 5px 0px 12px;
                        color: #000;
                    }

                    .dropdown-menu {
                        > :first-child {
                            padding-top: 4px;

                            svg {
                                margin-bottom: 3px;
                            }
                        }

                        > :last-child {
                            svg {
                                margin-bottom: 4px;
                            }
                        }
                    }

                    .dropdown-item:hover {
                        background-color: transparent;
                    }

                    .dropdown-item svg {
                        width: 16px;
                        height: 14px;
                        margin: 1.9px 4px 1.9px 0;
                        color: #000;
                    }

                    .dropdown-item span {
                        height: 16px;
                        margin: 0 0 0 0px;
                        color: #000;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: normal;
                        letter-spacing: normal;
                        line-height: 1.14;
                        text-align: left;
                        font-stretch: normal;

                        &:hover {
                            background-color: transparent;
                            color: #696969;
                        }
                    }
                }
            }

            .item-body {
                display: inline-block !important;
                flex: none !important;
                padding: 0.2px !important;
                background-color: #ffffff;
                overflow: hidden;
            }

            .template-loading-animation {
                position: absolute;
                top: 45%;
                left: 45%;
            }

            .item-footer {
                display: flex;
                position: relative;
                flex-wrap: nowrap;
                justify-content: space-between;
                min-height: 55px !important;
                padding-top: 17px !important;
                padding-right: 0 !important;
                padding-left: 0 !important;
                background-color: #ffffff !important;
                gap: 15px;

                .layout-builder-conditions {
                    display: flex;
                    max-width: 75%;
                    column-gap: 10px;
                    overflow: hidden;

                    .layout-builder-sitemap {
                        display: inline-flex;
                        max-width: 100%;
                        column-gap: 15px;

                        svg {
                            width: 20px;
                            height: 16px;
                            color: #a7b5c2;
                        }

                        .items-divider {
                            height: 17px;
                            border-right: 1px solid #AFAFAF;
                        }

                        .condition-text {
                            max-width: 75%;
                            color: #6c757d;
                            font-size: 12px;
                            font-style: normal;
                            font-weight: normal;
                            letter-spacing: normal;
                            line-height: 1.17;
                            text-align: left;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            overflow: hidden;
                            font-stretch: normal;

                            b {
                                color: #000;
                                font-size: 14px;
                                font-style: normal;
                                font-weight: bold;
                                letter-spacing: normal;
                                line-height: 1.25;
                                text-align: left;
                                font-stretch: normal;
                            }
                        }
                    }

                    .condition-edit, .meta-edit {
                        white-space: nowrap;
                        color: #222222;
                        font-size: 14px;
                        font-style: normal;
                        font-weight: normal;
                        letter-spacing: normal;
                        line-height: 1.14;
                        text-align: left;
                        text-decoration: underline;
                        cursor: pointer;
                        font-stretch: normal;

                        &:hover {
                            color: #696969;
                        }
                    }
                }

                .layout-builder-info {
                    display: flex;
                    column-gap: 15px;

                    .layout-builder-date {
                        display: flex;
                        min-width: 100px !important;
                        column-gap: 8px;

                        svg {
                            width: 14px;
                            height: 16px;
                            color: #a7b5c2;
                        }

                        span {
                            height: 14px;
                            padding-top: 1px;
                            color: #6c757d;
                            font-size: 12px;
                            font-style: normal;
                            font-weight: normal;
                            letter-spacing: normal;
                            line-height: 1.17;
                            text-align: left;
                            font-stretch: normal;
                        }
                    }

                    .layout-builder-author {
                        display: inline-flex;
                        column-gap: 8px;

                        svg {
                            width: 16px;
                            height: 16px;
                            color: #a7b5c2;
                        }

                        span {
                            height: 14px;
                            padding-top: 1px;
                            color: #6c757d;
                            font-size: 12px;
                            font-style: normal;
                            font-weight: normal;
                            letter-spacing: normal;
                            line-height: 1.17;
                            text-align: left;
                            font-stretch: normal;
                        }
                    }
                }
            }
        }

        .priority-15 {
            order: -1;
        }

        .priority-10 {
            order: 1;
        }

        .priority-5 {
            order: 2;
        }

        .large-height {
            .layout-builder-conditions {
                max-width: 73% !important;
            }

            .condition-text {
                max-width: 68% !important;
            }
        }

        &.items-one-column {
            grid-template-columns: 1fr;

            .medium-height {
                height: 306px;

                .item-body {
                    height: 190px !important;
                    min-height: auto !important;

                    iframe {
                        height: 190px !important;
                        min-height: auto !important;
                    }
                }
            }

            .large-height {
                height: 612px !important;

                .item-body {
                    height: 496px !important;
                    min-height: auto !important;

                    iframe {
                        height: 496px !important;
                        min-height: auto !important;
                    }
                }
            }

            .layout-builder-templates-item {
                .item-header {
                    height: 61px !important;
                }

                .item-footer {
                    height: 55px !important;
                    min-height: auto !important;
                }

            }
        }

    }
}

.layout-template-new-dialog {
    .jx-loading {
        min-width: 50px !important;
        min-height: auto !important;

        div {
            color: white !important;
        }
    }

    button.close {
        outline: none !important;

        &:focus {
            outline: none !important;
        }
    }
}

.layout-template-elementor-import-dialog-error {
    .btn {
        width: auto !important;
    }
}

.layout-template-new-dialog .btn-secondary,
.layout-builder-dialog-edit .btn-secondary,
.layout-template-delete-dialog .btn-secondary
{
    order: -2;
    width: 82px;
    height: 34px;
    margin-bottom: 27px !important;
    background-color: white !important;
    color: #6c757d !important;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    letter-spacing: normal;
    line-height: 1.21;
    text-align: center;
    font-stretch: normal;
}

.layout-template-new-dialog .btn-primary,
.layout-builder-dialog-edit .btn-primary,
.layout-template-delete-dialog .btn-primary
{
    width: auto;
    height: 34px;
    margin-bottom: 27px !important;
    margin-left: 19px !important;
}

.layout-template-new-dialog .modal-dialog,
.layout-builder-dialog-edit .modal-dialog,
.layout-template-delete-dialog .modal-dialog
{
    max-width: 600px !important;
}

.layout-template-new-dialog input,
.layout-builder-dialog-edit input,
.layout-template-delete-dialog input {
    height: 34px;
    margin: 10px 0px;
    width: 100%;
    border: solid 1px #ced4da;
    border-radius: 4px;
    background-color: #ffffff;
    padding: 6px 12px;
    font-size: 14px;
}

.layout-template-new-dialog .modal-footer,
.layout-builder-dialog-edit .modal-footer,
.layout-template-delete-dialog .modal-footer {
    padding-bottom: 0px !important;
    border-top: none !important;
}

.layout-template-delete-dialog .modal-title {
    height: 19px;
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: bold;
    letter-spacing: normal;
    line-height: 1.19;
    text-align: left;
    font-stretch: normal;
}

.layout-template-delete-dialog .desc-text {
    height: 16px;
    margin: 35.5px 33px 36px 24.5px;
    color: #000;
    font-size: 14px;
    font-style: normal;
    font-weight: normal;
    letter-spacing: normal;
    line-height: 1.14;
    text-align: left;
    font-stretch: normal;

    a {
        &:focus {
            box-shadow: none !important;
        }
    }
}

.layout-template-delete-dialog .rm-icon {
    width: 20px !important;
    height: 18px !important;
}

.layout-template-delete-dialog {
    button.btn-primary {
        display: flex;
        align-items: center;
        padding: 0 15px;
        column-gap: 10px;

        .spinner-border {
            color: #FFFFFF !important;
        }
    }
}



.jx-layout-builder-header-footer-settings {
    padding: 30px 0 0 20px;

    .jx-layout-builder-header-footer-settings-body {
        margin: 20px 0 0 0;

        .jx-overlap-wrapper {
            margin: 0 0 2px 0;

            div {
                margin-bottom: 8px;
            }
        }

        .jx-behaviors-wrapper {
            flex-direction: row;
            column-gap: 0px;

            > span {
                padding: 0;
                border-radius: 0 !important;

                label {
                    min-width: 100px;
                    min-height: 30px;
                    padding: 6px 6px;
                }
            }

            > button {
                display: inline-flex;
                border-radius: 0 !important;
            }
        }

        .save-succeed-result,
        .save-error-result {
            display: none;
        }

        .jx-fixed-header-settings {
            .jx-fixed-header-position {
                display: flex;
                flex-direction: row;
                column-gap: 0px;

                > span {
                    padding: 0;
                    border-radius: 0 !important;

                    label {
                        min-width: 100px;
                        min-height: 30px;
                        padding: 6px 6px;
                    }
                }
            }
        }

        .jx-sticky-header-settings {
            .jx-sticky-header-offset {
                span {
                    flex-direction: row;
                    align-items: center;
                    column-gap: 20px;

                    .jx-sticky-offset-px {
                        position: relative;
                        right: 40px;
                    }
                }
            }

            .jx-sticky-offset-input {
                width: 70px;
                padding-right: 20px;
                -webkit-appearance: none;
                -moz-appearance: none;
            }

            .jx-sticky-offset-input::-webkit-outer-spin-button,
            .jx-sticky-offset-input::-webkit-inner-spin-button {
                -webkit-appearance: none;
            }

            .jupiterx-layout-builder-header-sticky-template {
                width: 315px;
                max-width: 315px;

                input {
                  font-size: 16px !important;
                  line-height: 1.14;
                  box-shadow: none !important;
                }

                > div[class*="control"] {
                  height: 34px;
                  min-height: 34px;
                  border: 1px solid #ced4da;
                  box-shadow: none;

                  &:hover {
                    border-color: #635cff !important;
                    cursor: text;
                  }
                }

                div[class*="Input"] {
                  > div {
                    position: relative;
                    bottom: 5px;
                  }
                }
            }

            .sticky-templates-wrapper {
                position: relative;
                flex-direction: row;
                flex-wrap: wrap;
                align-content: center;
                align-items: center;
                justify-content: flex-start;
                column-gap: 5px;

                .clearer-icon {
                    position: absolute;
                    left: 290px;
                    cursor: pointer;
                    z-index: 999;
                }

                .hide-clearer {
                    display: none !important;
                }
            }

            .header-template-new-btn {
                display: inline-flex;
                align-items: center;
                max-height: 34px;
            }

            .header-template-edit-btn {
                display: inline-flex;
                align-items: center;
                max-height: 34px;
            }
        }
    }
}

.jupiterx-layout-builder-conditions-dialog {
    .modal-dialog {
        min-width: 1200px !important;

        .modal-content {
            min-height: 200px;

            .modal-title {
                margin-left: 6px;
                font-size: 20px;
            }

            .modal-body {
                padding: 0 !important;
            }
        }
    }

    .condition-content {
        display: initial;
        margin: 0 !important;
        padding: 0 !important;

        .jupiterx-form-condition-manager {
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            justify-content: space-between;
            height: 100%;
            margin-top: 0px;

            .jupiterx-condition-form-inner-wrapper {
                flex-grow: 1;
                max-height: 690px;
                padding-bottom: 25px;
                overflow-y: auto;
            }

            .jupiterx-conditions-rows-wrapper {
                position: relative;
                min-height: 250px;
                margin-right: 0px !important;
                margin-left: 30px !important;

                .row {
                    margin: 0 30px 8px 0 !important;
                    padding: 17px 37px 16px 10px !important;
                }

                .condition-row-add-button {
                    margin-top: 14px !important;
                    padding: 0 !important;
                }
            }
        }

        .condition-info {
            margin-bottom: 11px;
            padding: 19px 0 19px 30px;
            background: #FAFAFA 0% 0% no-repeat padding-box;

            > span {
                font-size: 12px;

                > span {
                    color: #6C757D;

                    b {
                        color: #007BFF;
                    }
                }
            }
        }

        .condition-header-desc {
            margin: 0px 30px 30px 30px;
            color: #6c757d;
            font-size: 14px;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.6em;
            text-align: left;
            font-stretch: normal;
        }

        .condition-header-back {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: baseline;
            height: 34px;
            padding: 4px 17px 9px 12.3px;
            border: solid 1px #6c757d;
            border-radius: 4px;
            background-color: #ffffff;

            svg {
                padding-top: 3px;
            }
        }

        .condition-header {
            margin: 30px 30px 9px 30px;
            color: #000;
            font-size: 20px;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.2;
            text-align: left;
            font-stretch: normal;
        }

        .condition-header-title {
            color: #000;
            font-size: 12px;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            text-align: left;
            font-stretch: normal;
        }

        .jupiterx-layout-builder-save-line {
            color: #F2F2F2;
            opacity: unset;
            border-top: 1px solid rgba(0,0,0,0.1);
        }

        .jupiterx-condition-submit-button {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-content: center;

            hr {
                width: 100%;
                margin-top: 0;
            }

            .condition-saver {
                margin: 4px 10px 20px 30px !important;
            }
        }

        #save-result {
            margin: 0 !important;
            padding-top: 14px;
        }
    }

    .modal-dialog-centered {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        align-items: center;
        justify-content: center;
    }

    .modal-content {
        width: 90vw !important;
        min-width: 90vw !important;
        height: 90vh
    }
}

.jupiterx-layout-builder-meta-edit-dialog {
    .modal-dialog-centered {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        align-items: center;
        justify-content: center;
    }

    .modal-content {
        width: 90vw !important;
        min-width: 90vw !important;
        height: 90vh
    }

    .modal-body {
        padding: 0 !important;
    }

    .jupiterx-layout-builder-meta-condition-content-wrapper {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: space-between;
        height: 100%;

        .meta-condition-content {
            flex-grow: 1;
            max-height: 680px;
            overflow: auto;
        }
    }

    .modal-header {
        height: 75px;

        .modal-title {
            margin-left: 6px;
            color: #000000;
            font-size: 20px;
            font-style: normal;
            font-weight: 500;
            line-height: 24px;
        }
    }

    .jupiterx-layout-builder-meta-condition-empty-message {
        margin-top: 150px;
        color: #000000;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        text-align: center;
    }

    .condition-info {
        margin-bottom: 11px;
        padding: 19px 0 19px 30px;
        background: #FAFAFA 0% 0% no-repeat padding-box;

        > span {
            font-size: 12px;

            > span {
                color: #6C757D;

                b {
                    color: #007BFF;
                }
            }
        }
    }

    .meta-conditions-header-description {
        margin: 25px 33px;

        .title-desc {
            display: block;
            color: #000000;
            font-size: 20px;
            font-style: normal;
            font-weight: 500;
            line-height: 24px;
        }

        .small-title {
            display: block;
            margin: 18px auto 22px;
            color: #6C757D;
            font-size: 12px;
            font-style: normal;
            font-weight: 500;
            line-height: 15px;
        }
    }

    .jupiterx-layout-builder-template-meta-conditions-item {
        min-height: 110px;
        margin: 15px 27px 15px 33px;
        padding: 17px 27px 19px 24px;
        background: #FAFAFA;

        .header-bar{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-content: center;
            align-items: center;
            justify-content: space-between;

            .jx-title {
                color: #000000;
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: 20px;
            }

            .jx-tools {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                align-content: center;
                align-items: center;
                justify-content: center;
                column-gap: 21px;

                .fa-trash-alt {
                    width: 16px;
                    height: 18px;
                    color: #FD4B49;
                    fill: #FD4B49;
                }

                .fa-edit {
                    width: 18px;
                    height: 18px;
                    color: #000000;
                    fill: #000000;
                }

                .fa-eye {
                    width: 20px;
                    height: 16px;
                    color: #000000;
                    fill: #000000;
                }

                svg {
                    cursor: pointer;
                }
            }
        }

        .jx-content {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            padding-top: 15px;
            row-gap: 15px;

            .jx-apply-changes {
                padding: 6px 18px;
                border: 1px solid #000000;
                border-radius: 2px;
                background: #FFFFFF;
                color: #000000;
            }
        }

        .dropdown-template-list {
            width: 50%;

            div[class*="control"] {
                align-content: center;
                height: 33px;
                min-height: 33px;
                box-shadow: none !important;

                &:focus {
                    box-shadow: none !important;
                }
            }

            span[class*="indicatorSeparator"] {
                display: none !important;
            }

            input[type="text"] {
                box-shadow: none !important;
            }
        }
    }

    .jupiterx-layout-builder-template-meta-conditions-footer {
        padding: 22px 25px 24px 33px;
        border-top: 1px solid #DBDBDB;

        .jx-close {
            padding: 9px 38px;
            border: none;
            border-radius: 2px;
            background: #000000;
            color: #FFFFFF;
            font-size: 14px;
            font-style: normal;
            font-weight: 500;
            line-height: 17px;
            text-align: center;
        }
    }
}

