.custom-snippet-list-table {
    .custom-snippet-start-card {
        max-width: 100%;
        padding: 0;

        .card-header {
            padding-top: 22px;
            padding-left: 25px;
            border-bottom: 0;
            background-color: transparent;
            font-size: 26px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        }

        .card-body {
            max-width: 500px;
            margin: 0 auto;
            padding-bottom: 66px;
        }

        img {
            margin-bottom: 4px;
        }

        .card-title {
            font-size: 26px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        }

        .card-text {
            color: #6c757d;
            font-size: 18px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        }

        #custom-snippet-empty-add-new {
            display: inline-flex;
            margin-top: 22px;
            padding: 18px 40px 18px 40px;
            border-radius: 2px;
            background-color: #000 !important;
            font-size: 20px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.25;
            letter-spacing: normal;
            text-align: center;
            color: #fff !important;
            cursor: pointer;
            flex-direction: column;
            flex-wrap: nowrap;
            justify-content: center;

            &:hover, &:active, &:focus {
                color: #fff !important;
                background-color: #696969 !important;
                border-color: #696969 !important;
            }
        }
    }

    .custom-snippets-list-header {
        display: flex;
        flex-direction: row;
        align-content: space-between;
        align-items: baseline;
        justify-content: flex-start;
        margin: -4px 0 5px 0;

        span {
            height: 31px;
            margin: 0 55px 44px 8px;
            color: #222;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-size: 26px;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.23;
            text-align: left;
            font-stretch: normal;
        }

        button {
            height: 34px;
            padding: 8px 20px 9px 24px;
            border-radius: 4px;
            background-color: #000;
            color: #fff;
            border-color: #000;
            border-radius: 2px !important;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.21;
            text-align: center;
            font-stretch: normal;
            box-shadow: none !important;
            outline: none !important;

            &:disabled, &:hover, &:focus, &:active {
                color: #fff !important;
                background-color: #000;
                border-color: #696969 !important;
            }
        }
    }

    .table-bulk-actions {
        display: flex;
        margin-bottom: 15px;

        select {
            width: auto;
            min-width: 135px;
            padding-top: 9px;
            padding-bottom: 9px;
            border: solid 1px #ced4da;
            border-radius: 4px;
            background-color: #fff;
            color: #222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.14;
            text-align: left;
            font-stretch: normal;
        }

        button {
            margin-left: 10px;
            padding: 8px 22px 9px;
            border: solid 1px #6c757d;
            border-radius: 4px;
            background-color: #fff;
            color: #6c757d;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.21;
            text-align: center;
            font-stretch: normal;
        }

        .jx-loading {
            min-height: auto !important;
        }
    }

    tr {
        border-bottom: 1px solid #dfdfdf !important;
    }

    // Bootstrap 5 puts "table-bordered" borders on cells (not on the table element like Bootstrap 4).
    // Our legacy table styling removes cell borders, so we need to explicitly re-add the outer border
    // on the table element to preserve the legacy look.
    .table {
        border: 1px solid #dfdfdf !important;
    }

    .table > :not(caption) > * > * {
        border: none !important;
    }

    th, td {
        position: relative;
    }

    th {
        height: 40px;
        padding-top: 0 !important;
        padding-bottom: 10px !important;
        color: #222;
        font-size: 14px;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-style: normal;
        font-weight: 500;
        font-weight: 500;
        letter-spacing: normal;
        line-height: 1.21;
        text-align: left;
        font-stretch: normal;
    }

    td {
        height: 60px !important;
        padding-top: 0 !important;
        padding-bottom: 0 !important;
        color: #222;
        font-size: 14px;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-style: normal;
        font-weight: normal;
        letter-spacing: normal;
        line-height: 1.43;
        text-align: left;
        vertical-align: middle !important;
        font-stretch: normal;
    }

    thead {
        background-color: #fafafa;

        th, td {
            background-color: #fafafa;
        }

        tr {
            border: 1px solid #dfdfdf !important;
        }
    }

    tbody tr {
        &:hover {
            background-color: #fafafa !important;
        }
    }

    .custom_snippets_table_head {
        th:nth-child(1) {
            width: 25% !important;

            span {
                position: absolute;
                top: 13px;
            }
        }

        th:nth-child(2) {
            width: 15% !important;
        }

        th:nth-child(3) {
            width: 15% !important;
        }

        th:nth-child(4) {
            width: 18% !important;
        }

        th:nth-child(5) {
            width: 18% !important;
        }

        th:nth-child(6) {
            width: 9% !important;
            text-align: left !important;
        }
    }

    .custom_snippets_table_body {
        .child-boxes, .parent-box {
            width: 16px;
            height: 16px;
            border: solid 1px #aeb5bd;
            border-radius: 3px;
            background-color: #fff;
        }

        td:nth-child(1) {
            position: relative;
            width: 25% !important;
            color: #222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: bold;
            letter-spacing: normal;
            line-height: 1.43;
            text-align: left;
            font-stretch: normal;

            span.custom-snippets-title {
                cursor: pointer;
            }
        }

        td:nth-child(2) {
            width: 15% !important;
            color: #222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.43;
            text-align: left;
            font-stretch: normal;
        }

        td:nth-child(3) {
            width: 15% !important;
            color: #222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.43;
            text-align: left;
            font-stretch: normal;
        }

        td:nth-child(4) {
            width: 18% !important;
            color: #007bff;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.43;
            text-align: left;
            font-stretch: normal;
        }

        td:nth-child(5) {
            width: 18% !important;
             color: #222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.43;
            text-align: left;
            font-stretch: normal;
        }

        td:nth-child(6) {
            width: 9% !important;
            color: #222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.43;
            text-align: left !important;
            font-stretch: normal;
        }
    }

    .jupiterx-custom-snippet-field-name {
        font-weight: 500;

        .title-wrapper {
            display: inline-flex;
            flex-direction: row;
            flex-wrap: nowrap;
            column-gap: 5px;
            justify-content: center;
            align-items: center;

            .badge {
                padding: 7px 17px;
                background-color: #ffeac1;
                color: #806c45;
                font-size: 13px;
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                font-weight: normal;
            }
        }

        .w-70 {
            width: 70%;
        }
    }

    .actions-wrapper {
        display: flex;
        position: relative;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: flex-start;
        padding-top: 2px;

        svg.fa-ellipsis-v {
            position: relative;
            bottom: 2px;
            left: 20px;
            box-shadow: none;
        }

        button {
            &:focus {
                box-shadow: none !important;
            }
        }

        a.jupiterx-custom-snippet-action-edit {
            display: flex;
            padding: 0;
            color: #222222;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.14;
            text-align: left;
            font-stretch: normal;
            text-decoration: underline;

            span {
                text-decoration: underline;
            }

            &:focus {
                box-shadow: none !important;
            }
        }
    }

    .jupiterx-custom-snippet-action-more {
        .dropdown-toggle {
            margin-top: 4px;
            color: #6c757d;

            svg {
                width: auto;
                height: 20px;
            }

            &::after {
                display: none;
            }
        }

        .dropdown-menu {
            max-width: 120px !important;
            min-width: 120px !important;
            padding: 8px 0 8px 1px;
            transform: translate(-90px, 32px) !important;
            display: flex;
            flex-flow: row wrap;
            place-content: center flex-start;
            align-items: baseline;
            row-gap: 0px;

            a {
                margin: 0px !important;
                font-size: 14px;
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                font-weight: normal;
                font-stretch: normal;
                font-style: normal;
                line-height: 1.14;
                letter-spacing: normal;
                text-align: left;
                color: #222;
            }

            a:nth-child(1) {
                padding: 6px 0 7px 11px;
            }

            a:nth-child(2) {
                padding: 6px 0 7px 11px;
            }
        }

        &.show {
            .dropdown-toggle {
                color: #222;
            }
        }
    }

    .jupiterx-custom-snippet-action-view {
        svg {
            margin-top: 2px;
        }
    }

    .jupiterx-custom-snippet-action-delete {
        color: #fd4b49 !important;
    }
}

.custom-snippets-add-new {
    hr {
        margin: 0 !important;
    }

    div.snippets-conditions-wrapper {
        .jupiterx-form-condition-manager {
            .jupiterx-layout-condition-row {
                margin: 0 0 8px 0;
            }
        }
    }

    div.step-wrapper {
        margin: 45px 0 50px 0;

        .steps-desc {
            display: block;
            height: 23px;
            margin-bottom: 17px;
            color: #000;
            font-size: 20px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: normal;
            letter-spacing: normal;
            line-height: 1.2;
            text-align: left;
            font-stretch: normal;
        }

        input, select {
            border: solid 1px #ced4da;
            border-radius: 4px;
            background-color: #fff;
            box-shadow: none !important;
        }

        .ace_print-margin {
            display: none !important;
        }
    }

    .header-wrapper {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: baseline;
        justify-content: space-between;

        .post-back-button {
            position: relative;
            display: inline-flex;
            padding: .375rem .75rem;
            font-weight: 400;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            line-height: 1.5;
            border-radius: .25rem;
            text-align: center;
            vertical-align: middle;
            flex-direction: column;
            flex-wrap: wrap;
            justify-content: center;
            align-content: center;

            svg {
                font-size: 19px;
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                position: relative;
                right: 5px;
                top: 1px;
            }
        }

        .header {
            display: block;
            height: 31px;
            margin: -16px 0 -5px 0;
            color: #222;
            font-size: 26px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.23;
            text-align: left;
            font-stretch: normal;
        }

        button {
            height: 34px;
            margin-left: 50px;
            border-radius: 4px;
            color: #fff;
            font-size: 14px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.21;
            text-align: center;
            font-stretch: normal;
        }
    }

    .jupiterx-form-condition-manager {
        margin-top: 5px;

        .condition-row-add-button {
            margin: 14px 0 0 0;
        }

        .jupiterx-condition-submit-button {
            padding-left: 1rem;

            .custom-snippet {
                margin: 16px 0 0 0;
                padding: 8px 18px 9px 15px;
                border: none !important;
                border-radius: 4px;
                background-color: #007bff;
                color: #fff;
                font-size: 14px;
                font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                font-style: normal;
                font-weight: 500;
                letter-spacing: normal;
                line-height: 1.21;
                text-align: center;
                font-stretch: normal;

                &:hover, &:active, &:focus {
                    border: none !important;
                    background-color: #007bff !important;
                    box-shadow: none !important;
                }
            }
        }
    }

    .submit-area {
        padding-top: 37px;

        .checkbox-wrap {
            padding-bottom: 19px;
        }

        .new-custom-snippets-submit {
            height: 46px;
            padding-right: 35px;
            padding-left: 35px;
            border-radius: 4px;
            background-color: #000 !important;
            color: #fff !important;
            border-color: #000 !important;
            font-size: 18px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.28;
            text-align: center;
            box-shadow: none !important;
            font-stretch: normal;

            &:hover, &:active, &:focus {
                color: #fff !important;
                background-color: #696969 !important;
                border-color: #696969 !important;
            }
        }

        .saving-mode {
            display: inline-flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: center;
            opacity: 0.5;

            span {
                margin-top: 4px;
            }
        }

        .remove-btn {
            height: 46px;
            padding-right: 29px;
            padding-left: 29px;
            border: solid 1px #fd4b49;
            border-radius: 2px;
            background-color: #fff;
            color: #fd4b49;
            font-size: 18px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-style: normal;
            font-weight: 500;
            letter-spacing: normal;
            line-height: 1.28;
            text-align: center;
            box-shadow: none !important;
            font-stretch: normal;
        }

        .show-error-result {
            color: red;
        }
    }
}

.custom-snippet-wrapper {
    .custom-snippet-start-card {
        border: none !important;

        .card-header {
            border: none !important;
        }

        .card-body {
            .card-title {
                margin-top: 45px;
