                font-style: normal;
                font-weight: 500;
                letter-spacing: normal;
                line-height: 1.23;
                text-align: center;
                font-stretch: normal;
            }

            .text-wrapper {
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                align-content: space-around;

                .card-text {
                    max-width: 489px;
                    height: 105px;
                    color: #6c757d;
                    font-size: 18px;
                    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                    font-style: normal;
                    font-weight: normal;
                    letter-spacing: normal;
                    line-height: 1.56;
                    text-align: center;
