            fill: #ff5c5c
        }

        .cls-6 {
            fill: #fff
        }
    }

}

.jupiterx-custom-snippet-delete-confirm{

    .modal-dialog {
        min-width: 520px !important;
    }

    .modal-footer {
        border-top: none !important;
    }
}
