.jx-cp-updates {
    &-actions {
        margin-bottom: 26px;
    }

    &-last-updated {
        font-size: 12px;
        line-height: 1.17;
        color: #6c757d;
        margin-top: 5px;
    }

    &-list {
        &-item {
            padding: 28px;
            min-height: 136px;
            border-radius: 4px;
            border: solid 1px #dfdfdf;
            background-color: #ffffff;

            &-details {
                flex-grow: 1;
                padding: 0 22px;
            }

            &-img {
                width: 80px;
                height: 80px;
                object-fit: cover;

                img {
                    height: 100%;
                    width: 100%;
                    border-radius: 4px;
                }
            }

            &-title {
                font-size: 16px;
                font-weight: bold;
                line-height: 1.19;
                color: #222222;
                margin-bottom: 10px;
            }

            &-cversion,
            &-nversion {
                font-size: 14px;
                line-height: 1.14;
                margin-bottom: 10px;
            }

            &-cversion {
                color: #222222;
            }

            &-nversion {
                color: #007bff;
            }

            &-action {
                .btn-success {
                    pointer-events: none;
                }
            }

            &-update-error {
                font-size: 12px;
                color: #ff0000;
                line-height: 1.14;
                text-align: right;
            }
        }
    }

    &-empty {
        font-size: 14px;
        line-height: 1.14;
        color: #222222;
    }

    @media screen and (max-width: 575.98px) {
        &-list {
            &-item {
                padding: 15px;

                &-details {
                    padding: 0;
                }

                &-img {
                    margin-bottom: 0.5rem;

                    img {
                        max-width: 80px;
                        max-height: 80px;
                    }
                }
            }
        }
    }
}
