@use 'sass:color';
@import '../../styles/bootstrap';

.jx-cp-version-control {
    margin-top: 35px;

    .nav-pills .nav-link {
        font-size: 15px;
        width: 100%;
    }

    .custom-control-label {
        line-height: 1.6;
    }

    // Version control tab.
    .jx-cp-version-control-content {
        .form-group-version {
            padding-bottom: 1rem;
            border-bottom: 1px solid #0000001a;

            label {
                width: 100%;
            }

            .custom-select {
                max-width: 100%;
                margin: 0 0 0.5rem;
            }

            .btn {
                min-width: 160px;
            }

            @include media-breakpoint-up(sm) {
                label {
                    width: auto;
                    margin-bottom: 0;
                }

                .custom-select {
                    width: 140px;
                    margin: 0 0.5rem;
                }
            }
        }


        button svg {
            margin: 0 3px 2px 0;
        }
    }

    @media only screen and (max-width: 575px ) {
        .nav-tabs .nav-link {
            font-size: 12px;
            padding: 8px 10px;
        }
    }
}
