.jupiterx-layout-builder {
    padding-bottom: 33px;

    .layout-builder-title {
        font-size: 26px;
        text-align: left;
        line-height: 32px;
        color: #000;
    }

    .layout-builder-description {
        padding-top: 15px;
        font-size: 14px;
        text-align: left;
        line-height: 16px;
        color: #6c757d;
    }

    .elementor-warning {
        span {
            font-weight: bold;
        }
    }
}

.jupiterx-layout-builder-list {
    margin-top: 0px !important;
    display: flex;
    flex-wrap: nowrap !important;

    .layout-list-wrap {
        min-width: 170px !important;
        max-width: 170px !important;
        padding-top: 20px;
        border-right: solid 1px #dfdfdf;
        background-color: #ffffff;
    }

    .list-focus {
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.13;
        letter-spacing: normal;
        text-align: left;
        color: #000 !important;
        border-right: 3px solid #000;
        background-color: #F2F2F2 !important;
    }

    .layout-menu-item {
        padding: 10px 15px !important;
        font-size: 16px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.13;
        letter-spacing: normal;
        text-align: left;
        color: #000;
        background-color: #ffffff;
        cursor: pointer;
        text-decoration: none !important;

        &:hover {
             color: #696969;
        }

        &:focus {
            box-shadow: none !important;
            outline: none !important;
        }
    }

    .templates-posts {
        flex-grow: 1;
    }
}

.jupiterx-layout-builder-list-blurred {
    pointer-events: none;
    filter: blur(4px);
}
