.jx-cp {
    .jx-cp-alert {
        display: flex;
        align-items: flex-start;

        .jx-cp-alert-icon {
            display: block;
            margin: 1px 0.6875rem 0 0;
            font-size: 1rem;

            svg {
                display: block;
            }
        }

        &.alert-success {
            .jx-cp-alert-icon {
                color: #4dc623;
            }
        }

        &.alert-warning {
            .jx-cp-alert-icon {
                color: #fcae29;
            }
        }

        &.alert-danger {
            .jx-cp-alert-icon {
                color: #fd4b49;
            }
        }
    }
}
