.popup-conditions-wrapper {
    margin: 40px 0 60px;

    .mb-3 {
        margin: 0 !important;
    }

    .popup-conditions-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .popup-conditions-row {
        width: 100%;
        max-width: 700px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 10px;
    }

    .popup-condition-select-wrapper {
        display: flex;
        flex-grow: 1;
        margin-right: 10px;
        width: 100%;
        max-width: 700px;
        box-shadow: none;
        border-radius: 3px;
        background: #D5D8DC;
        border: 1px solid #D5D8DC;
    }

    .popup-condition-type-group {
        position: relative;
        width: 120px;

        .popup-condition-type {
            color: #515962;
            background-color: #fff;
            font-size: 12px;
            border-width: 0;
            border-radius: 0;
            height: 40px;
            padding: 0 14px 0 33px;
            appearance: none;
            box-shadow: none;

            &:focus, &:active {
                color: #515962;
                background-color: #fff;
                box-shadow: none;
            }
        }

        &::before {
            content: '\e8cc';
            font-family: eicons;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 13px;
            font-size: 15px;
            color: #515962;
        }

        &::after {
            content: "\e8ad";
            font-size: 12px;
            font-family: eicons;
            position: absolute;
            top: 50%;
            right: 10px;
            color: #515962;
            pointer-events: none;
            transform: translate(-50%,-50%);
        }

        &.exclude-type {
            &::before {
                content: '\e8cd';
            }
        }
    }

    .popup-condition-name-group, .popup-condition-sub-name-group {
        position: relative;

        .popup-condition-name, .popup-condition-sub-name {
            color: #515962 !important;
            background-color: #ffffff !important;
            border-radius: 0;
            border-width: 0;
            height: 40px;
            padding: 0 14px;
            appearance: none;
            font-size: 12px;
            cursor: pointer;

            &:hover, &:focus, &:active {
                color: #515962 !important;
                background-color: #ffffff !important;
                box-shadow: none;
            }
        }

        &:after {
            color: #515962;
            content: "\e8ad";
            font-family: eicons;
            font-size: 12px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 10px;
            pointer-events: none;
        }

        &:not(:first-child) {
            flex-grow: 1;
            margin-left: 1px !important;
        }
    }

    .popup-condition-remove-group {
        button {
            color: #515962 !important;
            background-color: transparent !important;
            font-size: 18px;
            cursor: pointer;
            border: none;
            outline: none;
            box-shadow: none;
            margin: 0;
            padding: 0;

            &.btn-primary:not(.bg-transparent) {
                &:hover, &:focus, &:active, &:visited {
                    color: #515962 !important;
                    background-color: transparent !important;
                    box-shadow: none !important;
                }
            }

            &:hover, &:focus, &:active, &:visited {
                color: #515962 !important;
                background-color: transparent !important;
                box-shadow: none !important;
            }
        }
    }

    .popup-condition-sub-id-group {
        width: 158px;
        margin-left: 1px !important;

        .popup-condition-sub-id {
            > div {
                color: #6d7882;
                background-color: #ffffff;
                border-radius: 0;
                border-width: 0;
                min-height: 40px;
                padding: 0 0 0 15px;
                appearance: none;
                font-size: 12px;
                box-shadow: none;

                &[class*="-menu"] {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 0 !important;
                    padding: 0 !important;
                    border-top: 1px solid #eeeeee;

                    div[ class*="-option" ] {
                        padding: 5px 0 5px 10px !important;

                        &:hover {
                            color: #fff;
                            background-color: #5897fb;
                            text-align: left;
                        }
                    }

                    div[class*="-LoadingMessage"] {
                        padding: 5px 0 5px 10px !important;
                    }
                }

                > * {
                    color: #515962;
                }

                > div[class*="css-"] {
                    display: flex;
                }
            }

        }

        .popup-condition-sub-id-cross-icon {
            color: #6d7882;
            cursor: pointer;
            font-weight: 700;
            margin-right: 10px;
        }

        .popup-condition-sub-value-container {
            position: absolute;
            top: 100%;
            width: 100%;
            background: #fff;
            left: 0;
            right: 0;
        }
    }
}
