@use 'sass:color';
@use 'sass:meta';
@use 'sass:math';
@import './styles/bootstrap';

#jx-cp-root {
    padding: 1.875rem;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid $card-border-color;
    min-height: 300px;

    // @include media-breakpoint-down(xs) {
    //     padding: 1.875rem 1.25rem;
    // }
}

.jx-cp {
    @import '~bootstrap/scss/type';
    @import '~bootstrap/scss/images';
    // @import '~bootstrap/scss/code' // Depricated at v5.3.0;
    @import '~bootstrap/scss/reboot';
    @import '~bootstrap/scss/grid';
    @import '~bootstrap/scss/forms';
    @import '~bootstrap/scss/buttons';
    @import '~bootstrap/scss/transitions';
    @import '~bootstrap/scss/dropdown';
    @import '~bootstrap/scss/button-group';
    // @import '~bootstrap/scss/input-group'; // Depricated at v5.3.0;
    @import '~bootstrap/scss/forms/input-group';
    // @import '~bootstrap/scss/custom-forms'; // Depricated at v5.3.0;
    @import '~bootstrap/scss/nav';
    @import '~bootstrap/scss/navbar';
    @import '~bootstrap/scss/card';
    @import '~bootstrap/scss/badge';
    @import '~bootstrap/scss/alert';
    @import '~bootstrap/scss/list-group';
    @import '~bootstrap/scss/close';
    @import '~bootstrap/scss/spinners';
    @import '~bootstrap/scss/utilities';
    @import '~bootstrap/scss/utilities/api';
    @import '~bootstrap/scss/progress';
    @import '~bootstrap/scss/modal';
    @import '~bootstrap/scss/tooltip';
    @import '~bootstrap/scss/tables';
    // @import '~bootstrap/scss/breadcrumb';
    @import '~bootstrap/scss/pagination';
    // @import '~bootstrap/scss/jumbotron';
    // @import '~bootstrap/scss/media';
    // @import '~bootstrap/scss/toasts';
    // @import '~bootstrap/scss/popover';
    // @import '~bootstrap/scss/carousel';
    // @import '~bootstrap/scss/print';

    &,
    p {
        color: #000;
        font-size: 14px;
    }

    a {
        color: #222222;
        text-decoration: underline;

        &.jx-cp-theme-styles,
        &.nav-link,
        &.dropdown-item,
        &.btn {
            text-decoration: none;
        }

        &:hover, &:focus, &:active {
            color: #696969;
        }

        &.btn-primary {
            &:hover, &:active, &:focus {
                color: #fff !important;
                border-color: #696969 !important;
            }

            &:not(.bg-transparent) {
                &:hover, &:active, &:focus {
                    background-color: #696969 !important;
                }
            }
        }
    }

    .btn-outline-secondary, .btn-secondary {
        color: #000 !important;
        background-color: #fff !important;
        border-color: #000 !important;
        border-radius: 2px !important;

        &:hover, &:active, &:focus {
            color: #000 !important;
        }

        &:not(.bg-transparent) {
            &:hover, &:active, &:focus {
                background-color: #F2F2F2 !important;
            }
        }
    }

    .btn-outline-success {
        color: #28A745 !important;
        background-color: rgba(40, 167, 69, 0.1) !important;
        border-radius: 2px !important;
        border: none !important;
        padding: 5px 15px;
    }

    .btn-link {
        color: #222222;
        text-decoration: underline;

        &:hover, &:focus, &:active {
            color: #696969;
        }
    }

    // Reset the WP forms.css overrides.
    .card {
        margin: 0;
        padding: 0;
        box-shadow: none;
        max-width: none;
    }

    // Custom card header.
    .card-header {
        background-color: transparent;
        font-size: 16px;
        font-weight: 500;
        vertical-align: middle;

        > svg {
            margin-right: 5px;
            font-size: 24px;
        }

        span {
            vertical-align: middle;
        }
    }

    // Alerts.
    .alert {
        color: #000;
        padding: 9.5px 16px;

        &.alert-success {
            background-color: #f6ffec;
            border-color: #b6ed8a;
        }

        &.alert-warning {
            background-color: #fffbe5;
            border-color: #ffe689;
        }

        &.alert-danger {
            background-color: #fff2f0;
            border-color: #feccc6;
        }
    }

    // Forms.
    .form-check {
        padding-left: 1.75rem;

        .form-check-input {
            margin-left: -1.75rem;
        }
    }

    // Modals.
    .modal-:not(.modal-dialog) {
        max-width: 430px;
    }

    .modal-body {
        line-height: 1.5;
    }

    .modal-title {
        font-size: 1rem;
    }

    .modal-footer > * {
        margin-top: 0;
        margin-bottom: 0;
    }

    // Tooltips.
    .tooltip {
        font-size: 12px;

        .arrow {
            margin: auto;
        }
    }

    // form controls.
    .form-control {
        line-height: 1.5;
    }

    select.form-control {
        color: #000 !important;
        border-color: #CED4DA !important;
        min-height: 0;
        box-shadow: none !important;
        border-radius: 2px !important;

        &:hover, &:focus, &:active {
            box-shadow: none !important;
            background-color: #F2F2F2 !important;
        }
    }

    .form-control {
        color: #000 !important;
        border-color: #CED4DA !important;
        box-shadow: none !important;
        border-radius: 2px !important;
    }

    .form-check-input {
        height: 15px;

        &[type=radio]:checked:before {
            margin: 1rem;
        }

        &:checked {
            border-color: #000 !important;
            background-color: #000 !important;

            &:before {
                content: "";
            }

            ~ .form-check-label {
                &:before {
                    color: #fff !important;
                    border-color: #000 !important;
                    background-color: #000 !important;
                    box-shadow: none !important;
                }
            }
        }

        &:not(:disabled):active {
            ~ .form-check-label::before {
                background-color: color.adjust(#000, $lightness: 90%);
                border-color: color.adjust(#000, $lightness: 90%);
            }
        }

        &:focus {
            border-color: #CED4DA !important;
            box-shadow: none !important;

            ~ .form-check-label::before {
                box-shadow: none !important;
            }
        }
    }

    .form-check-label {
        padding: 0 5px;        
    }

    .modal-content {
        .btn-primary {
            color: #fff;
            border-color: #000;
            background-color: #000;
            box-shadow: none !important;
            outline: none;
            border: none;
            border-radius: 2px !important;
        }
    }

    .btn-primary {
        background-color: #000;
        color: #fff;
        border-color: #000;
        box-shadow: none !important;
        outline: none !important;
        border-radius: 2px !important;

        &:disabled, &:hover, &:focus, &:active {
            color: #fff !important;
            background-color: #000;
            border-color: #696969 !important;
        }

        &:not(.bg-transparent) {
            &:hover, &:active, &:focus {
                background-color: #696969 !important;
            }
        }
    }

    .form-switch {
        .form-check-input {
            &:focus {
                border-color: #000 !important;

                &:not(:checked) {
                    border-color: #CED4DA !important;
                    --bs-form-switch-bg: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%27-4 -4 8 8%27%3e%3ccircle r=%273%27 fill=%27rgba%280, 0, 0, 0.25%29%27/%3e%3c/svg%3e") !important;
                }
            }
        }

        .form-control-input {
            &:focus {
                box-shadow: none !important;

                ~ .form-check-label::before {
                    box-shadow: none !important;
                }
            }

            &:active:not(:disabled) {
                ~ .form-check-label::before {
                    background-color: color.adjust(#000, $lightness: 90%);
                    border-color: color.adjust(#000, $lightness: 90%);
                }
            }

            &:disabled:checked {
                & ~ .form-check-label::before {
                    background-color: rgba(0, 0, 0, 0.5);
                }
            }
        }
    }

    $utilities: map-merge(
        $utilities,
        (
            "width": (
            property: width,
            class: w,
            values: (
                25: 25%,
                50: 50%,
                75: 75%,
                100: 100%,
                auto: auto
            )
            ),
        )
    );

    .jupiterx-layout-builder-list.row > *:not(.col) {
        width: unset;
    }

    .modal {
        z-index: 100001;
    }

    .form-text {
        display: block;
    }

    .form-check-input[type="radio"] {
        left: 0;
        z-index: -1;
        width: 1rem;
        height: 1rem;

        &:checked {
            &:before {
                background-color: #fff !important;
            }

            &:focus {
                border-color: #000 !important;
            }
        }
    }

    svg.svg-inline--fa.fa-sm {
        vertical-align: middle;
    }
    
    .jx-cp-wrap-settings-contents {
        form > .row {
            margin-left: 5px;
            margin-right: 5px;
        }
    }

    .font-weight-bold {
        font-weight: 700 !important;
    }
}

#adminmenu {
    #toplevel_page_jupiterx {
        li {
            &.current {
                a {
                    color: rgba(240,246,252,.7);
                    font-weight: normal;
                }
            }

            &.active {
                a {
                    color: #fff;
                    font-weight: 600;
                }
            }
        }
    }
}
