.jupiterx-theme-update-modal {
    .modal-body {
        font-family: "inter";
        font-weight: 400;
        line-height: 24px;
    }

    .modal-title {
        width: 100%;
        justify-content: space-between;

        svg {
            width: 24px;
            height: 24px;
        }

        h3 {
            margin-bottom: 0;
            font-size: 1rem;
            font-weight: 500;
            line-height: 1.5;
        }

        .jx-loading {
            min-height: auto !important;
        }
    }

    .modal-content {
        .modal-footer {
            flex-direction: row-reverse;
            justify-content: right;

            .btn-primary, .btn-secondary {
                padding: 5px 15px 5px 15px;
                border-radius: 3px !important;
                font-family: 'inter';
                font-size: 14px;
                font-weight: 400;
            }

            .btn {
                .jx-loading {
                    min-height: auto;
                    padding: 5px 15px;
                }

                &.btn-primary {
                    .spinner-border {
                        color: #fff !important;
                    }
                }
            }
        }
    }
}
