#jx-cp-settings {
    margin-top: -5px;

    div[class*="jx-cp-settings-content"] {
        display: none;
        width: 100%;
        margin-top: 30px !important;
        margin-left: 35px !important;

        &.active {
            display: flex;
            flex-wrap: wrap;
        }
    }

    .nav-pills .nav-link {
        font-size: 15px;
        width: 100%;
    }

    .custom-control-label {
        line-height: 1.6;
    }

    .jx-cp-checkbox-wrap {
        column-count: 3;

        .custom-checkbox {
            min-height: auto;
        }
    }

    .col-sm-12 .jx-cp-checkbox-wrap {
        column-count: 4;
    }

    @media only screen and ( max-width: 575px ) {
        .nav-tabs .nav-link {
            font-size: 12px;
            padding: 8px 10px;
        }

        .jx-cp-checkbox-wrap {
            column-count: 2;
        }
    }

    margin-top: 0px !important;

    .jx-cp-settings-title {
        font-size: 26px;
        text-align: left;
        line-height: 32px;
        color: #222222;
        padding-bottom: 15px;
    }

    .jupiterx-settings-list-wrap {
        padding-top: 20px;
        border-right: solid 1px #dfdfdf;
        background-color: #ffffff;
    }

    .active-item {
        color: #000 !important;
        border-right: 3px solid #000;
        background-color: #F2F2F2 !important;
    }

    .jupiterx-settings-menu-item {
        padding: 10px 15px !important;
        font-size: 16px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.13;
        letter-spacing: normal;
        text-align: left;
        color: #000;
        background-color: #ffffff;

        &:focus {
            box-shadow: none;
        }

        &:hover {
            color: #696969;
        }

        &.disabled {
            display: flex;
            align-items: center;
            justify-content: space-between;

