                background-image: url("data:image/svg+xml,%3Csvg className='jx-cp-pro-badge' xmlns='http://www.w3.org/2000/svg' width='30' height='18'%3E%3Cg%3E%3Cpath fill='%23f23c97' d='M4 0h22a4 4 0 0 1 4 4v10a4 4 0 0 1-4 4H4a4 4 0 0 1-4-4V4a4 4 0 0 1 4-4z' /%3E%3Cpath fill='%23fff' d='M5.896 8.938v-2.62h1.86a1.747 1.747 0 0 1 1.185.335 1.239 1.239 0 0 1 .375.975 1.255 1.255 0 0 1-.375.98 1.7 1.7 0 0 1-1.185.33zm-.95-3.42v7.14h.95v-2.92h2.18a2.175 2.175 0 0 0 1.635-.55 2.12 2.12 0 0 0 .555-1.56 2.1 2.1 0 0 0-.555-1.555 2.217 2.217 0 0 0-1.635-.555zm6.48 0v7.14h.95v-3.05h2.32a1.261 1.261 0 0 1 .56.105.938.938 0 0 1 .34.28 1.161 1.161 0 0 1 .19.41q.06.235.1.5a3.485 3.485 0 0 1 .06.53l.02.505a3.082 3.082 0 0 0 .045.42.6.6 0 0 0 .135.3h1.06a1.221 1.221 0 0 1-.225-.415 3.253 3.253 0 0 1-.115-.495 4.6 4.6 0 0 1-.05-.53q-.01-.27-.03-.53a4.6 4.6 0 0 0-.085-.5 1.518 1.518 0 0 0-.17-.43 1.1 1.1 0 0 0-.31-.335 1.272 1.272 0 0 0-.5-.2v-.02a1.519 1.519 0 0 0 .945-.67 2.167 2.167 0 0 0 .3-1.14 1.721 1.721 0 0 0-.585-1.375 2.351 2.351 0 0 0-1.595-.5zm2.93 3.29h-1.98v-2.49h2.36a1.233 1.233 0 0 1 .97.34 1.281 1.281 0 0 1 .3.88 1.31 1.31 0 0 1-.135.635 1.068 1.068 0 0 1-.36.39 1.375 1.375 0 0 1-.525.2 3.829 3.829 0 0 1-.63.045zm4.47.28a4.116 4.116 0 0 1 .14-1.065 2.892 2.892 0 0 1 .44-.945 2.249 2.249 0 0 1 .77-.675 2.316 2.316 0 0 1 1.12-.255 2.316 2.316 0 0 1 1.12.255 2.249 2.249 0 0 1 .77.675 2.892 2.892 0 0 1 .44.945 4.116 4.116 0 0 1 .14 1.065 4.116 4.116 0 0 1-.14 1.07 2.892 2.892 0 0 1-.44.945 2.249 2.249 0 0 1-.77.675 2.316 2.316 0 0 1-1.12.255 2.316 2.316 0 0 1-1.12-.255 2.249 2.249 0 0 1-.77-.675 2.892 2.892 0 0 1-.44-.945 4.116 4.116 0 0 1-.14-1.07zm-.95 0a4.6 4.6 0 0 0 .215 1.4 3.54 3.54 0 0 0 .64 1.2 3.1 3.1 0 0 0 1.07.825 3.428 3.428 0 0 0 1.495.305 3.428 3.428 0 0 0 1.49-.305 3.1 3.1 0 0 0 1.07-.825 3.54 3.54 0 0 0 .645-1.195 4.6 4.6 0 0 0 .215-1.4 4.6 4.6 0 0 0-.215-1.4 3.54 3.54 0 0 0-.645-1.2 3.148 3.148 0 0 0-1.07-.835 3.381 3.381 0 0 0-1.49-.31 3.381 3.381 0 0 0-1.49.31 3.148 3.148 0 0 0-1.07.83 3.54 3.54 0 0 0-.645 1.2 4.6 4.6 0 0 0-.215 1.4z' /%3E%3C/g%3E%3C/svg%3E");
                content: '';
                display: inline-block;
                width: 30px;
                height: 18px;
                margin-right: 5px;
            }
        }

        &.hide-post-type {
            display: none;
        }
    }

    #modules {
        .jx-cp-checkbox-wrap {
            gap: 2px 24px;
            display: grid !important;
            grid-template-columns: repeat(4, minmax(0, 1fr));
            height: auto !important;
            align-content: initial;
            justify-items: start; /* RTL-aware start alignment */
        }

        .form-check {
            display: flex;
            gap: 6px;
            padding-left: 0 !important;
            align-items: center;

            .form-check-input {
                margin-left: 0 !important; /* LTR fallback */
                margin-inline-start: 0 !important; /* RTL-aware */
            }

            .form-check-label {
                padding-left: 0 !important; /* LTR fallback */
                padding-inline-start: 0 !important; /* RTL-aware */
                margin-bottom: -5px !important;
            }
        }

        /* Ensure consistent sizing across all sections */
        .jx-cp-element-group {
            padding-inline-start: 12px; /* RTL-aware subsection indent */

            .form-label {
                font-size: 13px; /* slightly smaller section titles */
                font-weight: 600;
            }

            .jx-cp-checkbox-wrap {
                margin-inline-start: 8px; /* indent checkboxes under each section title */
                /* override legacy width/height constraints */
                .custom-checkbox,
                .form-check {
                    width: auto !important;
                }
            }
        }

        .form-check-label {
            font-size: 13px; /* slightly smaller labels */
        }

        /* Responsive column counts */
        @media only screen and ( max-width: 1199px ) {
            .jx-cp-checkbox-wrap {
                grid-template-columns: repeat(3, minmax(0, 1fr));
            }
        }

        @media only screen and ( max-width: 991px ) {
            .jx-cp-checkbox-wrap {
                grid-template-columns: repeat(2, minmax(0, 1fr));
            }
        }

        @media only screen and ( max-width: 575px ) {
            .jx-cp-checkbox-wrap {
                grid-template-columns: repeat(1, minmax(0, 1fr));
            }
        }
    }
}
