.jx-cp {
    .jx-cp-comparison {
        table, th, td {
            border: 1px solid #dfdfdf;
        }

        table {
            width: 100%;
            border-collapse: collapse;
            color: #212529;
            line-height: normal;

            th, td {
                padding: 15px 20px;
            }

            tr {
                height: 48px;
            }

            th {
                height: 88px;
                background: #f7f7f7;
                line-height: 1.75;

                &.jx-ct-first-col {
                    width: 330px;
                }

                &.jx-ct-free {
                    width: 275px;

                    .jx-ct-desc {
                        color: #9aa7b3;
                    }
                }

                &.jx-ct-pro {
                    position: relative;
                    width: 275px;
                    background: #f23c97;

                    .jx-ct-title {
                        color: #ffffff;
                    }

                    .jx-ct-desc {
                        color: #ffbfe1;
                    }

                    .jupiterx-icon-pro {
                        position: absolute;
                        color: #ffbfe1;
                        font-size: 24px;
                        top: 15px;
                        right: 20px;

                        .rtl & {
                            right: initial;
                            left: 20px;
                        }
                    }
                }

                .jx-ct-title {
                    font-size: 28px;
                    font-weight: normal;
                }

                .jx-ct-desc {
                    display: block;
                    font-size: 14px;
                    font-weight: normal;
                    font-style: italic;
                }

            }

            td {
                font-size: 16px;

                &:last-of-type {
                    background: #fff7fb;
                }

                i {
                    font-size: 20px;
                }

                .jupiterx-icon-times {
                    color: #DF2E3F;
                    font-size: 26px;
                }

                .jupiterx-icon-check {
                    color: #0ACB82;
                }

                &.jx-ct-feature-label {
                    font-weight: bold;
                }

                &.jx-ct-bg-normal {
                    background: #ffffff;
                }
            }
        }

        a.jx-btn-upgrade-pro {
            width: 180px;
            height: 49px;
            padding-top: 8px;
            margin-bottom: 15px;
            font-size: 18px;
            line-height: 49px;
            padding: 0;
            background-color: #f23c97;
            color: #fff;
        }

        .jx-ct-learn-more {
            font-size: 14px;
            display: block;

            &:focus {
                text-decoration: underline;
            }
        }

        .jx-ct-text-center {
            text-align: center
        }
    }
}
