// Heading Font Family.
@import url( 'https://fonts.googleapis.com/css2?family=Big+Shoulders+Display' );
@import url( 'https://fonts.googleapis.com/css2?family=Yeseva+One' );
@import url( 'https://fonts.googleapis.com/css2?family=Rubik' );
@import url( 'https://fonts.googleapis.com/css2?family=Poppins' );
@import url( 'https://fonts.googleapis.com/css2?family=Playfair+Display' );
@import url( 'https://fonts.googleapis.com/css2?family=Pridi' );
@import url( 'https://fonts.googleapis.com/css2?family=Sora' );
@import url( 'https://fonts.googleapis.com/css2?family=Ubuntu' );

// Body Font Family.
@import url( 'https://fonts.googleapis.com/css2?family=Manrope' );
@import url( 'https://fonts.googleapis.com/css2?family=Mukta' );
@import url( 'https://fonts.googleapis.com/css2?family=Inter' );
@import url( 'https://fonts.googleapis.com/css2?family=Nunito+Sans' );
@import url( 'https://fonts.googleapis.com/css2?family=Roboto' );
@import url( 'https://fonts.googleapis.com/css2?family=Lato' );
