#adminmenu #toplevel_page_jupiterx li.current a {
    color: #fff !important;
    font-weight: 600 !important;
}

.jx-setup-wizard {
    margin: 20px 20px 20px 0;
    font-family: 'Inter';
    max-width: 1040px;
    margin-right: auto;
    margin-left: auto;

    .jx-setup-wizard-header {
        padding-top: 50px;
        padding-bottom: 75px;
        border-radius: 18px;
        background-repeat: no-repeat;
        background-size: cover;

        h3 {
            margin: 0 !important;
            color: #AEAEAE;
            font-size: 24.7047px;
            font-style: normal;
            font-weight: 700;

            sup {
                top: -18px;
                left: 10px;
                font-size: 10px;
                font-weight: 600;
                padding: 4px 6px 4px 6px;
                border: 1px solid #A4CFFF;
                border-radius: 4px;
                background: #DBECFF;
                color: #0077FF;
            }
        }

        .jx-setup-wizard-header-title {
            margin-bottom: 40px;
        }

        .list-group {
            flex-direction: row !important;
            justify-content: center;
            width: 100%;

            hr {
                width: 80px;
                height: 2px;
                margin: 15px 15px 0 15px;
                padding: 0;
                transition: all 0.3s;
                border: none;
                background: #E8E8E8;

                &.list-item-seprator-active {
                    background-color: #0077FF;
                }
            }

            .list-group-item {
                display: flex;
                position: relative;
                flex-direction: column;
                align-items: center;
                padding: 0;
                border: none;
                border-radius: 100px;

                button {
                    display: flex;
                    position: relative;
                    flex-direction: column;
                    align-items: center;
                    margin: 0 !important;
                    padding: 0 !important;
                    border: none !important;
                    background: transparent !important;

                    &.list-group-item-button-registration,
                    &.list-group-item-button-done {
                        pointer-events: none !important;
                    }
                }

                span {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 30px;
                    height: 30px;
                    transition: all 0.3s;
                    border: 2px solid #D1D1D1;
                    border-radius: 100px;
                    color: #969696;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 600;
                }

                h6 {
                    position: absolute;
                    top: 100%;
                    margin-top: 10px;
                    transition: all 0.3s;
                    color: #969696;
                    font-size: 15px;
                    font-style: normal;
                    font-weight: 500;
                }

                &.list-group-item-active {
                    span {
                        border-color: #0077FF;
                        background-color: #0077FF;
                        color: #fff;
                    }

                    h6 {
                        color: #111111;
                    }
                }
            }
        }
    }

    .jx-setup-wizard-content {
        padding: 62px 46px;
        border-radius: 18px;
        background: #FFFFFF;
        box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.04);
    }

    .jx-setup-wizard-content-step-registration {
        .step-registration-header {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;

            h2 {
                margin-bottom: 30px;
                color: #111111;
                font-size: 34px;
                font-style: normal;
                font-weight: 600;
            }

            p {
                color: #646464;
                font-size: 16px;
                font-weight: 400;
                line-height: 15px;
            }
        }

        .step-registration-form {
            width: 100%;
            margin: 50px auto 0;

            .step-registration-fields-wrapper,
            .step-registration-api-key-wrapper,
            .step-registration-terms-wrapper {
                display: grid;
                grid-template-rows: 1fr;
                transition: grid-template-rows 0.4s cubic-bezier(0.4, 0, 0.2, 1),
                            opacity 0.4s cubic-bezier(0.4, 0, 0.2, 1),
                            transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
                
                > div {
                    overflow: hidden;
                }
                
                &.visible {
                    grid-template-rows: 1fr;
                    opacity: 1;
                    transform: translateY(0);
                    visibility: visible;
                }
                
                &.hidden {
                    grid-template-rows: 0fr;
                    opacity: 0;
                    transform: translateY(-10px);
                    pointer-events: none;
                    
                    input, 
                    .custom-control {
                        pointer-events: none !important;
                        user-select: none;
                    }
                }
            }
            
            // Wrap children in inner container for grid animation
            .step-registration-fields-wrapper,
            .step-registration-api-key-wrapper,
            .step-registration-terms-wrapper {
                > div {
                    > .mb-3 {
                        min-height: 0;
                        width: 100%;
                        margin-bottom: 16px;

                        input[type="email"], 
                        input[type="text"] {
                            width: 100%;
                            margin: 0 !important;
                            padding: 12px 16px !important;
                            border: 1px solid #CED4DA !important;
                            border-radius: 4px !important;
                            background: #FFFFFF;
                            color: #646464 !important;
                            font-size: 14px;
                            font-style: normal;
                            font-weight: 400;
                            height: 44px;
                            transition: border-color 0.2s ease, box-shadow 0.2s ease;

                            &:focus {
                                outline: none;
                                border-color: #0077FF !important;
                                box-shadow: 0 0 0 3px rgba(0, 119, 255, 0.1);
                            }
                        }
                    }
                }
            }

            .step-registration-help-link-wrapper {
                transition: opacity 0.3s ease, 
                            transform 0.3s ease,
                            visibility 0.3s ease;
                
                &.visible {
                    opacity: 1;
                    transform: translateX(0);
                    visibility: visible;
                }
                
                &.hidden {
                    opacity: 0;
                    transform: translateX(10px);
                    pointer-events: none;
                    visibility: hidden;
                }
            }

            .step-registration-form-checkbox {
                margin-bottom: 10px;
                margin-top: 16px;

                label {
                    color: #646464;
                    font-size: 12px;
                    font-weight: 400;
                    line-height: 23px;
                }

                .custom-control-label::before {
                    border-color: #CCCCCC !important;
                    border-radius: 4px !important;
                }

                input:checked ~ .custom-control-label::before {
                    border-color: #0077FF !important;
                    background-color: #0077FF !important;
                }
            }

            .step-registration-form-api-key-active {
                .step-registration-form-api-icon {
                    color: #D1D1D1;
                }

                .step-registration-form-button {
                    color: #646464;
                }
            }

            .step-registration-form-api-icon {
                margin-right: 10px;
                color: #0077FF;
                font-size: 16px;
            }

            .step-registration-form-button {
                color: #0077FF;
                font-size: 14px;
                text-decoration: none;
                border: none;

                &:focus {
                    box-shadow: none;
                }
            }

            .step-registration-alert {
                margin-top: 20px;
                margin-bottom: -30px;
            }

            .step-registration-form-link {
                display: flex;
                align-items: center;
                width: max-content;
                color: #0077FF;
                font-size: 14px;
                font-weight: 400;
                text-decoration: none !important;
                box-shadow: none !important;
                line-height: 1.75;

                svg {
                    width: 16px;
                    height: 16px;
                    margin-right: 10px;
                    border-radius: 100px;
                    background-color: #fff;
                    color: #D1D1D1;
                    fill: #D1D1D1;
                }
            }
        }
    }

    .jx-setup-wizard-content-step-colors,
    .jx-setup-wizard-content-step-typography,
    .jx-setup-wizard-content-step-layout {
        flex-direction: column;

        .step-colors-header,
        .step-typography-header,
        .step-layout-header {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;

            h2 {
                color: #111111;
                font-style: normal;
                font-weight: 600;
                font-size: 34px;
                margin-bottom: 30px;
            }

            p {
                color: #646464;
                font-weight: 400;
                font-size: 16px;
                line-height: 28px;
                width: 50%;
                text-align: center;
            }
        }

        .step-color-body,
        .step-typography-body {
            margin: 0 auto;

            &> .row {
                margin: 0 !important;
                margin-top: 35px !important;
                gap: 40px;
                justify-content: flex-start;

                .typography-col,
                .color-col {
                    padding: 20px !important;
                    margin: 0 !important;
                    display: flex;
                    justify-content: center;
                    padding: 0 !important;
                    width: 227px;
                }

                .typography-col {
                    width: 276px;
                }
            }
        }

        .step-color-body {
            margin-left: 93px;
            margin-right: 94px;
        }
        
        .step-typography-body {
            margin-left: 20px;
            margin-right: 20px;
        }

        button:focus {
            outline: none;
        }
    }

    .jx-setup-wizard-content-step-typography {
        .jx-setup-wizard-typography-card-wrapper {
            width: 100%;
            border: none;
            background: transparent;
            padding: 0 !important;
            position: relative;

            &:hover {
                .jx-setup-wizard-typography-card {
                    box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.15);
                    transform: translateY(-2px);
                }
            }

            .jx-setup-wizard-typography-card {
                transition: all 0.3s;
            }

            .mark-holder {
                display: none;
            }
        
            &.selected {
                &:hover {
                    .mark-holder {
                        transform: translateY(-2px);
                    }
                }

                .jx-setup-wizard-typography-card {
                    border: 2px solid #07F;
                }
        
                .mark-holder {
                    width: 35px;
                    height: 35px;
                    display: block;
                    position: absolute;
                    right: 10px;
                    top: 10px;
                    background-image: url('data:image/svg+xml,<svg width="35" height="35" viewBox="0 0 35 35" fill="none" xmlns="http://www.w3.org/2000/svg"><g id="Group 5"><circle id="Ellipse 4" cx="17.5" cy="17.5" r="17.5" fill="%230077FF"/><path id="Vector 1" d="M9.9998 17L14.9999 22L24.7999 12" stroke="white" stroke-width="3" stroke-linejoin="round"/></g></svg>');
                    background-repeat: no-repeat;
                    z-index: 1;
                    transition: all 0.3s;
                }
            }
        }

        .jx-typography-customize__nav-bar {
            .customize-button {
                float: right;
            }

            .active-button {
                background: #F5F5F5 !important;
                border-color: #0077FF;
                border-width: 2px;
            }
        }

        .jx-typography-customize {
            .jx-choose-typography {
                transition: all 0.2s ease;
                padding: 20px 10px;
                border-radius: 10px;
                background: #F5F5F5;
                margin-top: 30px;
                position: relative;
                opacity: 0;
                margin-bottom: -100px;

                label {
                    color: #646464;
                    margin-right: 15px;
                    margin-bottom: 0;
                    width: 34%;
                    padding-left: 10px;

                    &[for="jx-body-font-size"] {
                        width: 26%;
                    }
                }

                .ratio {
                    display: flex;
                    align-items: center;
                    padding-left: 10px;
                    padding-right: 10px;
                }

                .body {
                    padding-left: 10px;
                    padding-right: 10px;
                }

                .jx-heading-ratio-select {
                    width: 100%;
                    display: inline-block;
                }

                input {
                    border-radius: 5px !important;
                    border: 1px solid #C9C9C9;
                    padding: 6px 13px;
                    padding-right: 5px;
                    width: 62px;
                    color: #666 !important;

                    &:focus {
                        border-color: #0077FF;
                        color: #666;
                    }
                }

                &.show-section {
                    margin-top: 30px;
                    opacity: 1;
                    margin-bottom: 30px;
                }

                .row {
                    align-items: center;
                }
            }

            .css-1okebmr-indicatorSeparator {
                display: none;
            }
        }

        .jx-typography-customize__sample {
            border-radius: 10px;
            border: 1px solid #C9C9C9;
            background: #FFF;
            box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.15);
            padding-top: 50px;
            padding-left: 30px;
            padding-right: 23px;
            padding-bottom: 33px;
            position: relative;

            &:before {
                content: "PREVIEW";
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%, -50%);
                background-color: #fff;
                padding: 10px 10px;
                z-index: 2;
                color: #8C8C8C;
                font-size: 14px;
                font-weight: 700;
            }

            .title {
                color: #9E9E9E;
                font-size: 18px;
                margin-bottom: 20px;
            }

            .text {
                width: 90%;

                p {
                    color: #666;
                }
            }

            .typography-customize-button {
                 a {
                    text-decoration: none;
                    width: 155px;
                    height: 46px;
                    border-radius: 6px;
                    font-size: 14px;
                    font-weight: 700;
                    line-height: 20px;
                    text-align: center;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: #6D6D6D;
                    transition: all 0.3s;

                    &:hover {
                        text-decoration: none;
                    }

                    .typography-customize-button-text {
                        color: #ffffff;
                    }
                }
            }

            .headings-wrapper {
                .heading {
                    overflow: hidden;
                }
            }
        }
    }

    .jx-setup-wizard-content-step-layout {
        .step-layout-body {
            .layout-section {
                margin-bottom: 70px;

                .header,
                .footer {
                    .title {
                        color: #000;
                        font-size: 21px;
                        font-weight: 700;
                    }

                    .text {
                        color: #646464;
                        font-size: 14px;
                        font-weight: 400;
                        margin-top: 15px;
                        margin-bottom: 15px;
                    }
                }

                .header-template-image {
                    width: 100%;
                    border-radius: 4px;
                    border: 1px solid #C9C9C9;
                }

                .footer-template-image {
                    width: 100%;
                    border-radius: 4px;
                    border: 1px solid #C9C9C9;
                }
            }
        }
    }

    .jx-setup-wizard-steps-control {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        margin-top: 50px;

        &.not-first {
            justify-content: end;
        }

        .skip-step {
            padding: 13px 33px;
            border-radius: 10px !important;
            border-color: #ccc !important;
            background-color: #fff !important;
            color: #ccc !important;
            font-size: 16px;

            &.btn-secondary:not(.bg-transparent):hover,
            &.btn-secondary:not(:disabled):not(.disabled):active,
            &.btn-secondary:not(.bg-transparent):focus,
            &:hover, &:focus, &:active {
                border-color: #f00 !important;
                background-color: #fff !important;
                color: #f00 !important;
                box-shadow: none;

                .jx-setup-wizard-spinner {
                    background: conic-gradient(#fff 180deg, #f00);
                }
            }

            .jx-setup-wizard-spinner {
                background: conic-gradient(#fff 180deg, #ccc);
            }
        }

        .next-step {
            padding: 13px 58px;
            border-radius: 10px !important;
            border-color: #0077FF !important;
            background-color: #0077FF !important;
            color: #fff !important;
            font-size: 16px;

            &.btn-primary:not(.bg-transparent):hover,
            &.btn-primary:not(:disabled):not(.disabled):active,
            &.btn-primary:not(.bg-transparent):focus,
            &:hover, &:focus, &:active {
                border-color: #0065D9 !important;
                background-color: #0065D9 !important;
                color: #fff !important;
            }

            .jx-setup-wizard-spinner {
                &::before {
                    background-color: #0077FF !important;
                }
            }

            &.btn-primary {
                &:disabled {
                    border-color: #0077FF !important;
    
                    &:hover {
                        background-color: #0077FF !important;
                    }
                }
            }
        }
    }

    .jx-setup-wizard-steps-form {
        max-width: 100%;

        .not-first {
            justify-content: end;
        }
    }

    .jx-cp-alert {
        width: max-content;
        padding: 10px !important;

        &.alert-danger {
            border: 1px solid rgba(247, 72, 80, 0.5);
            border-radius: 6px;
            background: rgba(247, 72, 80, 0.1);
            color: #F74850;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;

            .jx-cp-alert-icon {
                margin: 1px 10px 0 0;
                color: #F74850;
            }
        }
    }

    .jupiterx-setup-wizard-content-step {
        .step-content-header {
            text-align: center;

            h2 {
                margin-bottom: 16px;
                color: #111;
                font-size: 34px;
                font-weight: 600;
                line-height: 48px;
                text-align: center;
            }

            p {
                padding: 0;
                margin: 0;
                color: #646464;
                font-size: 16px;
                line-height: 28px;
                text-align: center;
            }
        }

        .step-website-type {
            margin-top: 54px;

            h4 {
                margin-bottom: 26px;
                color: #000;
                font-size: 21px;
                font-weight: 700;
                line-height: 26px;
            }

            .websites-type-box-wrap {
                display: grid;
                margin: 0 !important;
                column-gap: 52px;
                grid-template-columns: repeat(3, 1fr);

                .websites-type-box {
                    padding: 33px;
                    border: 1px solid #C9C9C9;
                    border-radius: 10px;
                    background: #FFF;
                    text-align: left;
                    transition: all 0.3s;
                    cursor: pointer;

                    h6 {
                        color: #000;
                        font-size: 18px;
                        font-weight: 700;
                        line-height: 26px;
                    }

                    p {
                        margin: 0;
                        padding: 0;
                        color: #666;
                        font-size: 14px;
                        line-height: 22px;
                    }

                    &:hover {
                        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.15);
                    }

                    .mark-holder {
                        display: none;
                    }
                }

                .active-site-type {
                    border-radius: 10px;
                    border: 2px solid #07F;
                    background: #FFF;
                    position: relative;
                    cursor: auto;

                    &:hover {
                        box-shadow: none;
                    }

                    .mark-holder {
                        width: 35px;
                        height: 35px;
                        display: block !important;
                        position: absolute;
                        right: 10px;
                        top: 10px;
                        background-image: url('data:image/svg+xml,<svg width="35" height="35" viewBox="0 0 35 35" fill="none" xmlns="http://www.w3.org/2000/svg"><g id="Group 5"><circle id="Ellipse 4" cx="17.5" cy="17.5" r="17.5" fill="%230077FF"/><path id="Vector 1" d="M9.9998 17L14.9999 22L24.7999 12" stroke="white" stroke-width="3" stroke-linejoin="round"/></g></svg>');
                        background-repeat: no-repeat;
                    }
                }
            }
        }

        .step-import-static, .step-import-dynamic {
            margin-top: 60px;

            > h4 {
                color: #000;
                font-size: 21px;
                font-weight: 700;
                line-height: 26px;
            }

            > p {
                margin-bottom: 37px;
                color: #646464;
                font-size: 14px;
            }

            .templates-wrap {
                display: grid;
                margin: 0 !important;
                column-gap: 53px;
                grid-template-columns: repeat(3, 1fr);
                row-gap: 50px;

                .step-single-template {
                    padding: 0;
                    display: flex;
                    flex-direction: column;
                    flex-wrap: nowrap;
                    justify-content: space-between;
                    border: 1px solid #C9C9C9;
                    border-radius: 10px;
                    background: #FFF;
                    overflow: hidden;
                    transition: all 0.3s;
                    height: fit-content;

                    &:hover {
                        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.15);
                    }

                    .action-bar {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: nowrap;
                        align-content: center;
                        align-items: center;
                        justify-content: space-between;
                        padding: 12px 12px 12px 26px;
                        border-top: 1px solid #C9C9C9;

                        button {
                            padding: 8px 39px;
                            border: 1px solid #C9C9C9 !important;
                            border-radius: 6px !important;
                            background: #FFF;
                            color: #666666 !important;
                            font-size: 14px;
                            font-weight: 500;
                            line-height: 26px;
                        }

                        span {
                            color: #000;
                            font-size: 14px;
                            font-weight: 700;
                            line-height: 26px;
                        }
                    }

                    img {
                        max-width: 100%;
                        height: 300px;
                        object-fit: cover;
                        object-position: top;
                    }

                    .mark-holder {
                        display: none;
                    }
                }

                .template-selected {
                    border-radius: 10px;
                    border: 2px solid #07F;
                    background: #FFF;
                    position: relative;

                    .mark-holder {
                        width: 35px;
                        height: 35px;
                        display: block !important;
                        position: absolute;
                        right: 10px;
                        top: 10px;
                        background-image: url('data:image/svg+xml,<svg width="35" height="35" viewBox="0 0 35 35" fill="none" xmlns="http://www.w3.org/2000/svg"><g id="Group 5"><circle id="Ellipse 4" cx="17.5" cy="17.5" r="17.5" fill="%230077FF"/><path id="Vector 1" d="M9.9998 17L14.9999 22L24.7999 12" stroke="white" stroke-width="3" stroke-linejoin="round"/></g></svg>');
                        background-repeat: no-repeat;
                    }
                }
            }
        }

        .step-import-sample-content-wrapper {
            margin-top: 38px;

