    .jx-setup-wizard-content-step-done {
        .jx-setup-wizard-steps-form {
            width: 100%;
        }

        .done-step-retry-btn {
            margin-left: auto;
            color: #07F;
            font-family: Inter;
            font-size: 14px;
            font-weight: 500;
            background: none;
            border: none;
            outline: none;
            
            &:hover {
                text-decoration: underline;
            }
        }
    }

    .jx-setup-wizard-steps-form {
        .tab-pane {
            width: 100%;
            display: none;

            &.active.show {
                display: block;
            }
        }
    }
}

.setup-wizard-step-content-popup,
.setup-wizard-step-layout-popup {
    .modal-content {
        border-radius: 18px !important;
    }

    .modal-header {
        display: flex;
        align-items: center !important;
        padding: 1.25rem 1.25rem;
        justify-content: space-between;
        flex-direction: row;
        flex-wrap: nowrap;
        padding: 22px 35px !important;
        border-radius: 18px 18px 0px 0px !important;
        border-bottom: 1px solid #C9C9C9 !important;
        background: #FFF !important;

        button {
            position: relative;
            right: 8px;
            margin: 0 !important;
            padding: 0 !important;
            &:focus {
                outline: none !important;
            }
        }

        .dark-toggle-wrap {
            display: flex;
            justify-content: center;
            position: absolute;
            width: 100%;
            right: 0px;
            left: 0;
        }

        .jx-setup-wizard-dark-mode-btn {
            width: 105px;
            margin: 0 auto !important;
            position: absolute;
            left: 0;
            right: 0;
        }

    }

    .modal-body {
        border-radius: 0px 0px 18px 18px !important;
        padding: 48px 50px !important;
        background: #F5F5F5;
        box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.04);
        overflow: auto;
        max-height: 600px;

        .popup-inner-container {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 45px;

            .single-template-preview-wrap {
                position: relative;
                border-radius: 10px;
                border: 1px solid #C9C9C9;
                background: #FFF;
                overflow: hidden;
                transition: all 0.3s;
                cursor: pointer;
                height: fit-content;

                &:hover {
                    > .fa-eye {
                        visibility: visible;
                        opacity: 1;
                    }
                }

                .fa-eye {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                    opacity: 0;
                    visibility: collapse;
                    cursor: pointer;
                    transition: all 0.2s;
                }

                img {
                    width: 100%;
                    max-width: 100%;
                    height: auto;
                    cursor: pointer;
                }

                &:hover {
                    box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.15);
                }
            }
        }
    }
}

.skip-wizard-modal {
    display: flex !important;
    align-items: center;
    justify-content: center;
    position: fixed;
    inset: 0px;
    padding-right: 20px;
    z-index: 99999;
    background: #00000066;

    .modal-dialog {
        width: 510px;
        height: 292px;
    }

    .modal-header {
        display: block;
        border: 0;
        padding: 0;
    }

    .modal-content {
        position: relative;
        background-color: #fff;
        border-radius: 18px;
        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.15);
        padding: 40px;
    }

    .modal-title {
        margin-top: 25px;
        margin-bottom: 10px;
        color: #111;
        font-family: Inter;
        font-size: 18px;
        font-weight: 700;
        line-height: 22px;
    }

    .modal-body {
        color: #646464;
        font-family: Inter;
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
        margin-bottom: 25px;
        padding: 0;
        width: 449px;
    }

    .modal-footer {
        display: flex;
        border: 0;
        padding: 0;
        justify-content: flex-start;

        .btn {
            display: inline-flex;
            padding: 12px 30px;
            justify-content: center;
            align-items: center;
            border-radius: 10px;
            border: none;
            font-family: Inter;
            font-size: 16px;
            font-weight: 500;
            line-height: 26px;
            cursor: pointer;
            margin: 0;
            height: 52px;
    
            &:first-child {
                background: #07F;
                color: #FFF;
                margin-right: 25px;
                transition: all 0.15s ease-in-out;
    
                &:hover {
                    background: #0065D9;
                }
            }
    
            &:nth-child(2) {
                background: #FFF;
                color: #666666;
                border: 1px solid #C9C9C9;
                transition: all 0.15s ease-in-out;

                &:hover {
                    background: #F5F5F5;
                }
            }
        }
    }
}

.setup-wizard-step-layout-popup {
    .modal-body {
        .popup-inner-container {
            grid-template-columns: auto;
        }
    }
}

.done-step-warning {
    div {
        color: #D09301;
        font-size: 14px;
    }
}

@media only screen and (max-width: 1199px) {
    .jx-setup-wizard {
        .templates-wrap {
            grid-template-columns: repeat(2, 1fr) !important;
        }
    }
}

@media only screen and (max-width: 768px) {
    .jx-setup-wizard {
        .templates-wrap {
            grid-template-columns: repeat(1, 1fr) !important;
        }
    }
}

@keyframes spin-right {
    100% {
      transform: rotate(360deg);
    }
}
