.jupiterx-cp-enable-widgets-modal {
    h4 {
        display: flex;
        align-items: center;
        margin: 0 !important;
        font-weight: 500 !important;
        font-size: 20px !important;
        line-height: 24px !important;
    }

    .modal-body {
        max-height: calc(100vh - 210px);
        padding: 0 !important;
        overflow-y: scroll;
    }

    .enable-widgets-header {
        width: 100%;
        height: 137px;
        background-color: #F4F4F4;
        padding: 1.25rem;

        figure {
            padding: 0 !important;
            margin: 0 !important;

            img {
                width: 128px !important;
                height: 67px !important;
            }

            figcaption {
                font-weight: 400;
                font-size: 15px;
                line-height: 26px;
            }
        }
    }

    .modal-form {
        padding: 1.25rem;

        .jx-cp-checkbox-wrap {
            columns: auto !important;
            display: flex;
            flex-direction: column;
            height: 100px;
            flex-wrap: wrap;
            align-content: baseline;

            .custom-checkbox {
                min-height: auto;
                width: 250px;
            }
        }

        .select-all {
            color: #6C757D !important;
            background-color: #fff !important;
            padding: 6px 18px;
            border: 1px solid #6C757D;
            line-height: 17px !important;
            font-weight: 500;
        }


        div:nth-of-type( 2 ) {
            label {
                margin-top: 0 !important;
            }
        }
    }

    .modal-footer {
        justify-content: space-between !important;

        .btn-primary {
            padding: 6px 33px;

            .jx-loading {
                .spinner-border {
                    color: #fff !important;
                }
            }
        }

        .btn-secondary {
            color: #6C757D !important;
            background-color: transparent !important;
            padding: 6px 33px;
            border: 1px solid #6C757D;
        }

        .btn {
            .jx-loading {
                min-height: auto;
                padding: 4px 33px;
            }
        }
    }

    @media only screen and ( max-width: 768px ) {
        .enable-widgets-header {
            width: 100%;
            height: max-content;
            background-color: #F4F4F4;
            padding: 1.25rem;

            figure {
                flex-wrap: wrap;
                justify-content: center !important;

                img {
                    width: 128px !important;
                    height: 67px !important;
                }

                figcaption {
                    width: 100% !important;
                }
            }
        }
    }
}
