.jx-cp-element-group{
    .form-label {
        font-size: 14px;
        font-weight: 700;
    }

    .form-text {
        font-size: 12px;
        font-weight: 500;
    }

    .jx-cp-checkbox-wrap {
        columns: auto !important;
        display: flex;
        flex-direction: column;
        height: 90px;
        flex-wrap: wrap;
        align-content: baseline;

        .custom-checkbox {
            min-height: auto;
            width: 240px !important;
        }
    }

    &.jx-cp-element-group-large {
        .jx-cp-checkbox-wrap {
            height: 270px !important;
        }

        &.jx-cp-element-group-1 {
            .jx-cp-checkbox-wrap {
                height: 110px !important;
            }
        }

        &:last-child {
            .jx-cp-checkbox-wrap {
                height: 160px !important;
            }
        }
    }

    @media only screen and ( max-width: 768px ) {
        .jx-cp-checkbox-wrap {
            height: 200px !important;
        }

        &.jx-cp-element-group-large {
            .jx-cp-checkbox-wrap {
                height: 400px !important;
            }

            &:last-child {
                .jx-cp-checkbox-wrap {
                    height: 300px !important;
                }
            }
        }
    }

    @media only screen and ( max-width: 768px ) {
        .jx-cp-checkbox-wrap {
            height: max-content !important;
        }

        &.jx-cp-element-group-large {
            .jx-cp-checkbox-wrap {
                height: max-content !important;
            }

            &.jx-cp-element-group-1 {
                .jx-cp-checkbox-wrap {
                    height: max-content !important;
                }
            }

            &:last-child {
                .jx-cp-checkbox-wrap {
                    height: max-content !important;
                }
            }
        }
    }
}

.jx-cp-settings-content-elementor {
    overflow: hidden;
}
