@import '../../../../styles/bootstrap';

.jx-cp {
    .jx-cp-search-filters {
        display: flex;
        flex-flow: row wrap;
        margin: 0 -30px 30px;
        padding: 0 30px 19px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.125);
    }

    .jx-cp-search-filter-s {
        width: 100%;
        min-height: 3.75rem;
        margin-bottom: 1rem;

        .input-group,
        .input-group > * {
            height: 100%;
        }

        .form-control,
        .form-control:focus {
            background-color: #f2f6f7;
            border-radius: 0;
            border-color: #ced4da;
        }

        .input-group-text {
            display: flex;
            justify-content: center;
            width: 68px;
            border: none;
            border-radius: 0;
            border-color: #ced4da;
            background-color: #222;
            color: #fff;
        }
    }

    .jx-cp-search-filters-toggle.btn {
        display: flex;
        align-items: center;
        margin-right: 1rem;
        border: 1px solid #ced4da;
        border-radius: 0;
        color: #000;

        svg {
            margin-right: 0.73rem;
        }
    }

    .jx-cp-search-filters-overlay {
        position: fixed;
        top: 100%;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 999999;
        overflow: auto;
        width: 100%;
        background-color: #fff;

        &.active {
            top: 0;
            transition: top 0.15s ease-in-out;
        }
    }

    .jx-cp-search-filters-overlay-header {
        display: flex;
        align-items: center;
        padding: 1.25rem;
        border-bottom: 1px solid rgba(0, 0, 0, 0.125);

        .btn-secondary {
            margin-left: auto;
            margin-right: 0.5rem;
        }
    }

    .jx-cp-custom-select {
        &.show .dropdown-toggle {
            background-color: #f2f5f7;
        }

        .dropdown-toggle {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            position: relative;
            width: 100%;
            padding: 0.5625rem 1.25rem 0.5625rem 0.8125rem;
            background-color: #fcfeff;
            outline: none;
            border: 1px solid #ced4da;
            border-radius: 0;
            box-shadow: none;
            font-size: 0.875rem;
            font-weight: 400;
            text-align: left;
            white-space: nowrap;
            transition: background-color 0.15s ease-in-out;

            &:after {
                border-top: 0.4375rem solid;
                border-right: 0.25rem solid transparent;
                border-left: 0.25rem solid transparent;
                color: #495056;
            }

            &:hover {
                background-color: #f2f5f7;
            }

            & > div {
                max-width: calc(100% - 15px);
            }
        }

        .dropdown-menu {
            min-width: 100%;
            margin: -1px 0;
            padding: 0.875rem;
            border: 1px solid #ced4da;
            border-radius: 0;
        }

        .card {
            border-radius: 0;
            border-top: none;
            border-left: none;
            border-right: none;
        }

        .card-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            min-height: 3.567rem;
            font-size: 0.875rem;

            &:after {
                content: '';
                display: inline-block;
                margin-left: 0.255em;
                border-top: 0.4375rem solid;
                border-right: 0.25rem solid transparent;
                border-left: 0.25rem solid transparent;
                border-bottom: 0;
                color: #495056;
                vertical-align: 0.255em;
            }
        }

        .btn {
            &:focus {
                box-shadow: none !important;
            }
        }
    }

    .jx-cp-custom-select-clear {
        margin-left: 0.5rem;
        color: #66aeff;
        font-size: 0.75rem;
        font-weight: normal;
        text-transform: none;
    }

    .jx-cp-custom-select-title {
        font-weight: bold;
        text-transform: uppercase;
    }

    .jx-cp-custom-select-selected {
        overflow: hidden;
        margin-top: 0.25rem;
        color: $secondary;
        text-overflow: ellipsis;
    }

    @media (min-width: 783px) {
        .jx-cp-search-filters {
            justify-content: space-between;
            padding-left: 26px;
            padding-right: 26px;
        }

        .jx-cp-search-filter {
            flex: 0 1 25%;
            width: 25%;
            padding: 0 4px 8px;
            margin: 0;
        }

        .jx-cp-search-filter-s,
        .jx-cp-search-filter-category {
            flex-basis: 50%;
            width: 50%;
        }

        .jx-cp-search-filter-s {
            margin-bottom: 0;
        }
    }
}
