@use 'sass:color';
@import '../../styles/bootstrap';

.jx-cp {
    .jx-cp-dashboard {
        .card-columns {
            column-count: 1;
            column-gap: 28px;
            max-width: 750px;

            .card {
                max-width: 750px;
                margin-bottom: 31px;
            }
        }

        .jx-site-health {
            .alert {
                margin-bottom: 9px;
            }

            .alert:last-of-type {
                margin-bottom: 0;
            }
        }

        .card-body{
            .jx-cp-dashbord-license-bulk-note {
                font-size: 12px;
                color: #080808d6;
            }

            .jx-cp-dashbord-license-bulk-note-link {
                font-size: 13px;
            }
        }

        .jx-cp-license,
        .jx-cp-support {
            .card-header {
                padding: 0.75rem 1.25rem;
            }
        }
    }

    .jx-cp-dashboard-documentation {
        .card-body {
            margin-bottom: -12px;
        }

        ul {
            margin-bottom: 0;
            list-style: none;

            @include media-breakpoint-up(sm) {
                column-count: 2;
            }

            @media (min-width: 1440px) {
                column-count: 3;
            }
        }

        li {
            position: relative;
            padding-left: 17px;
            margin-bottom: 12px;

            &::before {
                content: '';
                position: absolute;
                top: 6px;
                left: 0;
                height: 6px;
                width: 6px;
                background-color: #a7b5c2;
                border-radius: 10px;
            }
        }
    }

    .jx-cp-performance-tips {
        .list-group-item {
            padding: 12px 0;

            svg {
                margin-right: 7px;
                margin-top: -2px;
                color: #a7b5c2
            }
        }
    }

    .jupiterx-cp-pro-banner {
        position: relative;
        margin-bottom: 30px;
        padding: 46px 32px 28px;
        background: #fafafa url('../../../assets/images/control-panel/pro-banner.jpg') no-repeat center 25%;  
        background-size: cover;
        border-radius: 10px;
        color: #fff;

        .jupiterx-icon-pro {
            position: absolute;
            top: 29px;
            right: 31px;
            font-size: 23px;
        }

        h1 {
            margin-bottom: 36px;
            font-size: 38px;
            font-weight: 300;
            color: #fff;
        }

        .btn {
            margin-bottom: 25px;
        }

        .jx-cp-features {
            display: flex;

            @include media-breakpoint-down(sm) {
                display: block;
            }
        }

        ul {
            margin: 0 6% 0 0;
            font-size: 16px;

            li {
                display: flex;
            }

            .jupiterx-icon-check-solid {
                margin: 1px 9px 0 0;
                opacity: 0.5;

                .rtl & {
                    margin: 1px 0 0 9px;
                }
            }
        }
    }

    .jx-cp-dashboard-sellkit {
        &.dismiss-sellkit {
            display: none !important;
        }

        .sellkit-alert-wrapper {
            > div {
                width: 100%;
                justify-content: space-between;
                display: flex;
            }

            img {
                width: 100px;
                height: 20px;
                object-fit: cover;
            }

            .sellkit-notice-dismiss {
                color: #000;
                border: none !important;
                box-shadow: none !important;
                padding: 0 !important;
                text-decoration: none;

                &::before {
                    content: '';
                    background: none;
                    color: #000;
                    content: "\f153";
                    display: block;
                    font: normal 16px/10px dashicons;
                    speak: never;
                    text-align: center;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                    border: none !important;
                    box-shadow: none !important;
                }
            }
        }

        .card-body {
            padding-bottom: 0.5rem;
        }

        .card-footer {
            background: transparent;
            border: none;
            padding-top: 0;
            padding-bottom: 1.25rem;
            display: flex;
            align-items: end;
            justify-content: space-between;

            .btn-link {
                outline: none;
                border: none !important;
                box-shadow: none;
                text-decoration: none;
                color: #6c757d;

                &:hover {
                    border: none !important;
                }
            }

            .sellkit-footer-wrapper {
                display: flex;
                align-items: center;

                .jx-loading {
                    padding: 0.5rem;
                    display: flex;
                    align-items: center;
                    border: 1px solid #6c757d;
                    border-radius: 0.2rem;
                    justify-content: center;
                    width: 127px;
                    min-height: max-content;
                }
            }

            .sellkit-text-wrapper {
                display: flex;
                flex-direction: column;

                span {
                    font-weight: 600;
                    font-size: 12px;
                }
            }
        }

        .sellkit-features {
            columns: 2;

            span {
                background: #000;
                color: #fff;
                width: 15px;
                height: 15px;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                border-radius: 100px;
                padding: 3px;
                margin-right: 5px;

                svg {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }

    .dashboard-register-tooltip-link {
        color: #fff !important;
    }
}
