
.jx-popup-admin-wrapper {
    right: 0;
    background: #ffffff;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.0394871);
    display: block;
    position: absolute;
    top: 0;
    width: calc(100% - 160px);
    z-index: 1;

    .jx-popup-admin-header {
        display: flex;
        height: 50px;
        align-items: center;
        padding: 0 16px;

        button {
            background: #f7f7f7;
            border: 1px solid #0275d8;
            border-radius: 3px;
            box-sizing: border-box;
            color: #0275d8;
            cursor: pointer;
            display: inline-block;
            line-height: 30px;
            margin: 0 4px;
            min-height: 30px;
            padding: 0 10px;
            text-decoration: none;
        }

        .logo {
            display: flex;
            align-items: center;
            margin-inline-end: 40px;

            h6 {
                color: #495157;
                font-size: 15px;
                font-weight: normal;
                padding: 0 8px;
                line-height: normal;
                text-transform: uppercase;
                margin: 0 !important;
            }
        }
    }

    .logo {
        svg {
            max-width: 100px;
        }
    }
}

.jx-add-new-popup-dialog {
    .modal-dialog {
        max-width: 600px !important;
    }

    .modal-body {
        padding: 1.25rem;
    }
    .modal-footer {
        border: none !important;
        padding-top: 0 !important;

        .jx-loading {
            min-height: auto !important;
            display: block !important;
        }

        .spinner-border {
            color: #ffffff !important;
        }

        .btn {
            padding: 0.25rem 1rem !important;
        }

        .btn-primary {
            order: 2;
        }

        .btn-outline-secondary {
            order: 1;
        }
    }
}

.jx-filtered-files-popup-import-dialog {
    .modal-dialog {
        width: 375px !important;
    }

    .modal-header {
        justify-content: center !important;
        padding: 0 !important;
        margin: 0 !important;
        border: none !important;

        .modal-title  {
            font-size: 15px;
            color: #495157;
            padding: 30px 0 10px;
            font-weight: 500;
            text-align: center;
        }
    }

    .modal-body {
        text-align: center;
        font-size: 12px;
        line-height: 1.5;
        color: #3c434a;
        overflow: hidden;
    }

    .modal-footer {
        flex-direction: row-reverse;
        padding: 0 !important;
        margin: 0 !important;

        button {
            color: #39b54a !important;
            background-color: transparent !important;
            width: 50% !important;
            margin: 0 !important;
            padding: 13px 0;
            border: none !important;
            border-radius: 0 !important;
            font-size: 15px !important;
            line-height: 1.4em;
            box-shadow: none !important;
            text-align: center !important;

            &:first-child {
                color: #b01b1b !important;
                border-right: 1px solid #e6e9ec !important;
            }

            &:hover {
                background-color: #f4f6f7 !important;
            }
        }
    }
}

#jx-popup-root ~ #wpbody #wpbody-content {
    margin-top: 50px;

    #jupiterx-import-template-area {
        display: none;
        margin: 50px 0 30px;
        text-align: center;
    }

    #jupiterx-import-template-title {
        font-size: 18px;
        color: #555d66;
    }

    #jupiterx-import-template-form {
        display: inline-block;
        margin-top: 30px;
        padding: 30px 50px;
        background-color: #FFFFFF;
        border: 1px solid #e5e5e5;
    }
}


// Conditions and Trigger.
.jx-conditions-trigger-popup-dialog {
    height: 100% !important;

    > .modal-dialog {
        width: 100% !important;
        height: 100% !important;
        margin: auto !important;

        .modal-content {
            height: 100%;
        }
    }

    .modal-header {
        padding: 0 15px!important;
        background-color: #fff;
        position: relative;
        z-index: 1;
        height: 50px;
        border: none !important;
        border-bottom: 1px solid #E6E8EA !important;

        .jx-conditions-trigger-popup-dialog-header, .modal-title {
            width: 100%;
            height: 50px;
        }

        .close-button {
            color: #515962;
            background: #fff;
            width: 47px;
            margin-right: -15px !important;
            border: none;
            border-left: 1px solid #E6E8EA;
            outline: none !important;

            i {
                color: #515962;
                font-size: 20px;
                cursor: pointer;
            }
        }

        .popup-dialog-header-content {
            span {
                background: #000;
                color: #fff;
                display: inline-block;
                font-size: 12px;
                line-height: 1;
                margin-right: 10px;
                width: 30px;
                height: 30px;
                border-radius: 6px;
                padding: 7px;
            }

            h4 {
                font-size: 13px;
                color: #0C0D0E;
                line-height: 1;
                text-transform: uppercase;
                font-weight: bold;
                padding: 0 !important;
                margin: 0 !important;
            }
        }
    }

    .modal-footer {
        flex-direction: row-reverse;
        justify-content: right !important;
        background-color: #fff;
        border: none;
        justify-content: flex-end;
        padding: 10px !important;
        box-shadow: none;
        position: relative;
        border-top: 1px solid #E6E8EA;

        .btn {
            border: none !important;
            outline: none !important;
        }

        .btn-secondary {
            text-transform: uppercase !important;
            background-color: transparent !important;
            color: #515962 !important;
            font-weight: normal;
            font-size: 12px;
            font-weight: 500;
            line-height: 1.2;
            margin: 0 0 0 5px !important;
            padding: 8px 16px;
            box-shadow: none !important;
            transition: all 0.3s;

            &:hover {
                background-color: #F1F2F3 !important;
                color: #3f444b !important;
            }
        }
    }

    .modal-body {
        z-index: 99999;
        overflow: auto;
        height: 100% !important;
        padding: 0 !important;
        flex: auto;
    }

    form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100vh;
    }

    &.jx-popup-dialog-conditions {
        .modal-footer {
            .btn-primary {
                display: flex;
                align-items: center;
                background-color: #F3BAFD !important;
                color: #0C0D0E !important;
                margin: 0 0 0 5px !important;
                padding: 8px 16px;
                font-size: 12px;
                font-weight: 500;
                line-height: 1.2;
                text-transform: uppercase !important;
                box-shadow: none !important;
                transition: all 0.3s;

                &:hover {
                    background-color: #F5D0FE !important;
                }
            }
        }
    }

    &.jx-popup-dialog-triggers {
        .modal-footer {
            .btn-primary {
                display: flex;
                align-items: center;
                background-color: transparent !important;
                color: #D004D4 !important;
                margin: 0 0 0 5px !important;
                padding: 8px 16px;
                font-size: 12px;
                font-weight: 500;
                line-height: 1.2;
                text-transform: uppercase !important;
                box-shadow: none !important;
                transition: all 0.3s;

                &:hover {
                    background-color: #FAE8FF !important;
                }
            }

            .btn-secondary {
                display: none !important;
            }
        }
    }
}

@media (max-width: 1439px) {
    .jx-conditions-trigger-popup-dialog > .modal-dialog {
        max-width: 990px !important;
    }
}
@media (min-width: 1440px) {
    .jx-conditions-trigger-popup-dialog > .modal-dialog {
        max-width: 1200px !important;
    }
}

.jupiterx-popup-condtion-trigger-row {
    flex-wrap: nowrap !important;
    height: 100%;
    margin: 0 !important;
    overflow-x: hidden;

    .jupiterx-popup-tabs-nav {
        width: 25%;
        max-width: 25%;
        height: 100%;
        padding-top: 50px;
        background-color: #fff;
        border-inline-end: 1px solid #E6E8EA;

        .nav-link {
            background-color: transparent !important;
            border: none !important;
            border-radius: unset !important;
            box-shadow: none !important;
            position: relative;
            height: 110px;
            padding: 20px 15px;
            cursor: pointer;

            h5 {
                color: #515962;
                font-size: 18px;
                font-weight: bold;
            }

            p {
                color: #515962;
                font-size: 13px;
                margin: 0 !important;
            }

            img {
                width: 50px;
                height: auto;
            }

            &:hover {
                background-color: #F1F2F3 !important;
            }

            &.active {
                background-color: #E6E8EA !important;
                box-shadow: none !important;
                position: relative;
                color: #0C0D0E;

                h5, p {
                    color: #0C0D0E;
                }

                &::after {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    width: 5px;
                    background-color: #0C0D0E;
                }
            }
        }
    }

    .jupiterx-popup-tabs-content {
        flex-grow: 1;
        height: 100%;
        overflow: auto;
        padding: 50px;
        width: unset;

        .conditions-triggers-header-wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;

            .conditions-triggers-logo {
                width: 70px;
                margin-top: 45px;
            }

            .conditions-triggers-title {
                color: #515962;
                margin-top: 20px;
                font-size: 30px;
                font-weight: normal;
                text-align: center;
            }

            .conditions-triggers-text {
                color: #515962;
                margin-top: 15px;
                font-size: 18px;
                font-weight: 300;
                line-height: 1.5;
                text-align: center;
            }
        }

        .popup-add-new-conditions {
            color: #fff !important;
            background-color: #515962 !important;
            margin: 50px auto 0 auto;
            padding: 12px 26px;
            font-size: 12px;
            font-weight: 500;
            line-height: 1.2;
            text-transform: uppercase;
            border: none !important;
            border-radius: 3px !important;
            outline: none;
            transition-duration: 0.3s;
            transition-property: background, color, box-shadow, opacity, -webkit-box-shadow;

            &.btn-primary:not(.bg-transparent) {
                &:hover, &:focus, &:active {
                    color: #fff !important;
                    background-color: #3a4046 !important;
                    box-shadow: none;
                }
            }

            &:hover, &:focus, &:active {
                color: #fff !important;
                background-color: #3a4046 !important;
                box-shadow: none;
            }
        }
    }
}

