.jx-popup-theme-dark {
    .modal-content,
    .modal-header,
    .modal-footer,
    .jupiterx-popup-tabs-nav,
    .close-button {
        background-color: var( --e-a-bg-default ) !important;
        border-color: var( --e-a-border-color ) !important;
    }

    .jupiterx-popup-condtion-trigger-row {
        .jupiterx-popup-tabs-content {
            .conditions-triggers-header-wrapper {
                .conditions-triggers-title,
                .conditions-triggers-text {
                    color: var( --e-a-color-txt ) !important;
                }
            }
        }

        .popup-conditions-wrapper {
            .popup-condition-select-wrapper {
                border-color: var( --e-a-border-color-bold ) !important;
                background-color: var( --e-a-border-color-bold ) !important;
            }

            .popup-condition-type-group {
                .popup-condition-type {
                    background-color: #1a1c1e !important;
                    color: #babfc5 !important;
                }

                &:before {
                    color: #babfc5 !important;
                }

                &:after {
                    color: var( --e-a-color-txt ) !important;
                }
            }

            .popup-condition-name-group {
                .popup-condition-name {
                    background-color: #1a1c1e !important;
                    color: #babfc5 !important;
                }

                .popup-condition-sub-name {
                    background-color: #1a1c1e !important;
                    color: #babfc5 !important;
                }

                &:after {
                    color: var( --e-a-color-txt ) !important;
                }
            }

            .popup-condition-sub-name-group {
                .popup-condition-name {
                    background-color: #1a1c1e !important;
                    color: #babfc5 !important;
                }

                .popup-condition-sub-name {
                    background-color: #1a1c1e !important;
                    color: #babfc5 !important;
                }

                &:after {
                    color: var( --e-a-color-txt ) !important;
                }
            }

            .popup-condition-sub-id-group {
                .popup-condition-sub-id > div {
                    background-color: #1a1c1e !important;

                    > * {
                        color: #babfc5 !important;
                    }
                }
            }

            .popup-condition-remove-group {
                button {
                    color: var( --e-a-color-txt ) !important;
        
                    &.btn-primary:not(.bg-transparent) {
                        &:hover, &:focus, &:active, &:visited {
                            color: var( --e-a-color-txt ) !important;
                        }
                    }
        
                    &:hover, &:focus, &:active, &:visited {
                        color: var( --e-a-color-txt ) !important;
                    }
                }
            }
        }

        .jupiterx-popup-tabs-nav {
            .nav-link {
                h5,
                p {
                    color: var( --e-a-color-txt ) !important;
                }

                &.active,
                &:hover {
                    background-color: #1a1c1e !important;
                }

                &.active:after {
                    background-color: var( --e-a-border-color-bold ) !important;
                }
            }
        
        }
    }

    &.jx-conditions-trigger-popup-dialog {
        &.jx-popup-dialog-conditions {
            .modal-footer {
                .btn-secondary {
                    color: #f0abfc !important;
                    
                    &:hover,
                    &:focus {
                        color: #f0abfc !important;
                        background-color: #310132 !important;
                    }
                }
            }
        }

        &.jx-popup-dialog-triggers {
            .modal-footer {
                .btn-primary {
                    color: #f0abfc !important;
                    
                    &:hover,
                    &:focus {
                        color: #f0abfc !important;
                        background-color: #310132 !important;
                    }
                }
            }
        }
    }

    .modal-header {
        .popup-dialog-header-content {
            h4 {
                color: var( --e-a-color-txt-active ) !important;
            }

            .header-logo {
                background: var( --e-a-bg-logo );

                svg path {
                    fill: var( --e-a-color-logo );
                }
            }
        }

        .close-button {
            border-left-color: var( --e-a-border-color ) !important;

            i {
                color: var( --e-a-color-txt ) !important;
            }
        }
    }

    .popup-triggers-wrapper {
        .popup-trigger-select-wrapper {
            background-color: #1a1c1e !important;
            border-color: var( --e-a-border-color-bold ) !important;
        }

        .popup-trigger-remove-group {
            .btn.btn-primary {
                .eicon-close {
                    color: var(--e-a-color-txt) !important;
                }

                &:hover {
                    background-color: transparent !important;
                }
            }
        }

        .popup-trigger-name-group,
        .popup-trigger-operator-group {    
            .popup-trigger-name,
            .popup-trigger-operator {
                background-color: #1a1c1e !important;
                border-color: var(--e-a-border-color-bold) !important;
                color: #babfc5 !important;
    
                &:hover, &:focus, &:active {
                    background-color: #1a1c1e !important;
                    border-color: var(--e-a-border-color-bold) !important;
                    color: #babfc5 !important;
                }
            }
        }

        .popup-trigger-row {
            .form-label {
                color: #babfc5 !important;
            }

            .popup-trigger-control {
                &.form-control {
                    background-color: #1a1c1e !important;
                    border-color: var(--e-a-border-color-bold) !important;
                    color: #babfc5 !important;

                    &:focus {
                        background-color: #1a1c1e !important;
                        border-color: var( --e-a-border-color-bold ) !important;
                        color: #babfc5 !important;
                    }
                }
            }

            .popup-trigger-type-date,
            .popup-trigger-type-date-range {
                border-color: var( --e-a-border-color-bold ) !important;
                color: #babfc5 !important;
        
                &:focus, &:active {
                    border-color: var( --e-a-border-color-bold ) !important;
                }

                &.jupiterx-popup-date-input-open {
                    border-color: var( --e-a-border-color-bold ) !important;
                }
        
                .jupiterx-popup-date-input {
                    color: #babfc5 !important;
                    background-color: transparent;
                }
            }

            .popup-trigger-type-time-range {
                input,
                select {
                    background-color: #1a1c1e !important;
                    border-color: var(--e-a-border-color-bold) !important;
                    color: #babfc5 !important;
                }

                select:hover {
                    background-color: #1a1c1e !important;
                }
            }
        }
    }
}