.jx-cp {
    .jx-cp-templates {
        display: flex;
        flex-flow: row wrap;
        margin-left: -10px;
        margin-right: - 10px;

        .jx-cp-pro-badge {
            position: absolute;
            top: -5px;
            right: -5px;
        }

        .card-img-top {
            padding-top: 63%;
            background-size: cover;
            background-color: rgba(0, 123, 255, 0.03);
        }

        .card {
            width: 100%;
            margin: 10px;
            transition: 0.15s ease-in-out;

            &:hover {
                box-shadow: 0px 7px 14px rgba(0, 0, 0, 0.05), 0px 3px 7px rgba(0, 0, 0, 0.07);
                transform: translate(0, -2px);
            }
        }

        .card-body {
            .btn-outline-primary {
                color: #000 !important;
                border-color: #000 !important;
                background-color: transparent !important;
                box-shadow: none !important;
                border-radius: 2px !important;

                &:hover {
                    background-color: #f2f2f2 !important;
                }
            }
        }
    }

    .jx-cp-shadow-card {
        &.card,
        &.card:hover {
            border: none;
            background: none;
            box-shadow: none;
            transform: none;
        }

        .card-title {
            display: block;
            width: 50%;
            height: 1.75rem;
            margin-bottom: 15px;
        }

        .btn {
            display: inline-block;
            width: 25%;
            height: 1.75rem;
            border-radius: 0;
        }

        .card-img-top,
        .card-title,
        .btn {
            background-color: #eee;
            background: darkgray;
            background: linear-gradient(to right, #eeeeee 10%, #dddddd 18%, #eeeeee 33%);
            background-size: 800px 104px;
            border: none;
            animation-duration: 1.25s;
            animation-fill-mode: forwards;
            animation-iteration-count: infinite;
            animation-name: jx-cp-shadow-card;
            animation-timing-function: linear;
            pointer-events: none;
        }
    }

    .jx-cp-templates-empty {
        width: 100%;
        margin: 3rem 0;
        text-align: center;

        svg {
            color: #9ba8b4;
            font-size: 50px;
        }

        h3 {
            margin: 1rem 0 0.5rem;
            font-size: 1.625rem;
            font-weight: bold;
        }

        p {
            margin: 0;
            color: #6c757d;
            font-size: 1.25rem;
        }
    }

    @media (min-width: 680px) {
        .jx-cp-templates {
            .card {
                width: calc(50% - 20px);
            }
        }
    }

    @media (min-width: 1140px) {
        .jx-cp-templates {
            .card {
                width: calc(33.3333333% - 20px);
            }
        }
    }

    @media (min-width: 1510px) {
        .jx-cp-templates {
            .card {
                width: calc(25% - 20px);
            }
        }
    }

    @media (min-width: 1810px) {
        .jx-cp-templates {
            .card {
                width: calc(20% - 20px);
            }
        }
    }
}

@keyframes jx-cp-shadow-card {
	0% {
		background-position: -468px 0
	}
	100% {
		background-position: 468px 0
	}
}

.simplicity-import-notice {
    span {
        font-family: 'inter';
        font-weight: 400;
        line-height: 24px;

        strong {
            font-weight: 700;
        }
    }

    .modal-content {
        .modal-footer {
            .btn-primary, .btn-secondary {
                padding: 5px 15px 5px 15px;
                border-radius: 3px !important;
                font-family: 'inter';
                font-size: 14px;
                font-weight: 400;
            }
        }
    }
}
