.jx-setup-wizard-spinner {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border-radius: 100px;
    background: conic-gradient(#0077FF 180deg, #fff);
    animation: rotating 2s linear infinite;

    &::before {
        position: absolute;
        width: 75%;
        height: 75%;
        border-radius: 100px;
        background-color: #fff;
        content: '';
    }
}

@keyframes rotating {
    100% {
        transform: rotate(360deg);
    }
}

// Color Palettes Component.
.jx-setup-wizard-color-palettes {
    width: 100%;
    border: 1px solid #C9C9C9;
    border-radius: 10px !important;
    overflow: auto !important;
    background: transparent;
    padding: 0;
    transition: all 0.3s;
    position: relative;

    &:hover {
        box-shadow: 0px 6px 14px 0px #00000026;
        transform: translateY(-2px);
    }

    &:focus {
        border: 1px solid #0077FF;
    }

    .color-palette__color {
        height: 112px;
    }
}

// Choose Colors Component.
.jx-choose-colors {
    .jx-choose-colors__nav-bar {
        .customize-button {
            float: right;
        }

        .active-button {
            background: #F5F5F5 !important;
            border-color: #0077FF;
            border-width: 2px;
        }
    }

    .jx-choose-colors__sample {
        border: 1px solid #C9C9C9;
        border-radius: 10px;
        padding: 50px;
        position: relative;
        border-radius: 10px;
        box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.15);
        margin-top: 30px;
        margin-bottom: 30px;

        &:before {
            content: "PREVIEW";
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: #fff;
            padding: 10px 10px;
            z-index: 2;
            color: #8C8C8C;
            font-size: 14px;
            font-weight: 700;
        }

        .jx-choose-colors__icon {
            width: 58px;
            height: 58px;
            font-size: 25px;
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            fill: #fff;
        }

        .jx-choose-colors__text {
            p {
                color: #666;
                font-size: 14px;
                line-height: 20px;

                a {
                    color: #2A9D8F;
                    text-decoration: none;
                    font-weight: 900;
                }
            }
        }

        .jx-choose-colors__button {
            a {
                text-decoration: none;
                width: 155px;
                height: 46px;
                border-radius: 6px;
                font-size: 14px;
                font-weight: 700;
                line-height: 20px;
                text-align: center;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .jx-choose-colors__button-text {
                color: #fff;
            }
        }
    }

    .jx-setup-wizard-color-palettes {
        margin: 0 auto !important;
        height: 45px;
        cursor: auto;
        justify-content: end;

        &:focus {
            border: 1px solid #C9C9C9;
        }

        &:hover {
            box-shadow: none;
            transform: none;
        }

        .color-palette__color {
            height: auto;
        }
    }

    .jx-choose-colors__choose {
        transition: all 0.2s ease;
        padding: 20px 10px;
        border-radius: 10px;
        background: #F5F5F5;
        margin-top: 30px;
        position: relative;
        opacity: 0;
        margin-bottom: -116px;
        display: flex;
        justify-content: center;

        .color-picker-col {
            padding-right: 10px;
            padding-left: 10px;
            flex: 1;
        }

        .choose-color-wrapper {
            border-radius: 10px;
            border: 1px solid #C9C9C9;
            display: flex;
            align-items: center;
            cursor: pointer;
            text-align: left;
            background: #fff;
            transition: all 0.3s;
            height: 46px;
            padding: 10px 15px;
            position: relative;
            width: 100%;

            .color {
                width: 24px;
                height: 24px;
                border-radius: 50%;
                border: 2px solid #E6E6E6;
                flex: none;
            }

            .text {
                font-size: 14px;
                font-weight: 500;
                line-height: 26px;
                width: 88%;
                padding-left: 10px;
                border-radius: 5px;
                border: 1px solid transparent;
                height: 38px;
                line-height: 38px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            &:hover {
                box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.15);
            }

            &.active {
                border: 1px solid #0077FF;
                box-shadow: none;
            }

            .sketch-picker {
                position: absolute;
                top: 47px;
                left: 0;
                z-index: 3;
            }

            button {
                border: none;
                padding: 0;
            }
        }

        &.show-section {
            margin-top: 30px;
            opacity: 1;
            margin-bottom: 30px;

            .row {
                justify-content: center;
                display: flex;
                flex-wrap: wrap;
            }
        }

        @media screen and (max-width: 1200px) {
            .color-picker-col {
                min-width: 0;
            }
        }
    }

    .jx-choose-colors__divider {
        hr {
            border-width: 3px;
        }
    }
}

// Dark Mode Component.
.jx-setup-wizard-dark-mode-btn {
    text-align: center;
    border: none;
    padding: 0;
    margin: 0;
    background: transparent;

    .toggle-checkbox {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        z-index: 1;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
    }

    .toggle-slot {
        position: relative;
        height: 45px;
        width: 100px;
        border: 1px solid #C9C9C9;
        border-radius: 10em;
        background-color: #F8F8F8;
        transition: background-color 250ms;
    }

    .toggle-checkbox:checked ~ .toggle-slot {
        background-color: #646464;
    }

    .toggle-button {
        transform: translate(60px, 6px);
        position: absolute;
        height: 30px;
        width: 30px;
        border-radius: 50%;
        box-shadow: inset 0 0 0 1px #666666;
        transition: background-color 250ms, border-color 250ms, transform 500ms cubic-bezier(.26,2,.46,.71);
    }

    .toggle-checkbox:checked ~ .toggle-slot .toggle-button {
        background-color: #646464;
        box-shadow: inset 0 0 0 100px #FFFF;
        transform: translate(7px, 7px);
    }

    .sun-icon {
        position: absolute;
    }

    .sun-icon-wrapper {
        position: absolute;
        opacity: 1;
        transform: translate(8px, 12px);
        transform-origin: 50% 50%;
        transition: opacity 150ms,transform 500ms cubic-bezier(0.26, 2, 0.46, 0.71);
    }

    .toggle-checkbox:checked ~ .toggle-slot .sun-icon-wrapper {
        opacity: 0;
        transform: translate(3em, 2em);
    }

    .moon-icon {
        position: absolute;
        color: #FFFF;
    }

    .moon-icon-wrapper {
        position: absolute;
        opacity: 0;
        transform: translate(11em, 2em) rotate(0deg);
        transform-origin: 50% 50%;
        transition: opacity 150ms,transform 500ms cubic-bezier(0.26, 2, 0.46, 0.71);
    }

    .toggle-checkbox:checked ~ .toggle-slot .moon-icon-wrapper {
        opacity: 1;
        transform: translate(65px, 10px);
    }

    label {
        position: relative;
        margin-bottom: 0 !important;
        display: block;
    }
}

// Nav Button Component.
.jx-setup-wizard-nav-btn {
    border: 1px solid #C9C9C9;
    border-radius: 10px !important;
    width: max-content;
    padding: 10px 21px;
    background: transparent !important;
    transition: all ease 0.2s;

    .text {
        color: #666666;
        font-size: 16px;

        img {
            margin-right: 10px;
            font-size: 15px;
            margin-top: -4px;
        }
    }

    &:hover {
        background: #F5F5F5 !important;
    }

    &.change-button {
        height: 45px;
        display: flex;
        align-items: center;
    }
}


// Typography Card Component.
.jx-setup-wizard-typography-card {
    border: 1px solid #C9C9C9;
    border-radius: 10px;
    padding: 37px;
    text-align: left;

    .typography-card-button {
        button {
            text-decoration: none;
            width: 155px;
            height: 46px;
            border-radius: 6px;
            font-size: 14px;
            font-weight: 700;
            line-height: 20px;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #6D6D6D;
            transition: all 0.3s;
            border: none;
            color: #ffffff;

            &:hover {
                text-decoration: none;
            }

            .typography-card-button-text {
                color: #ffffff;
            }
        }
    }

    p {
        color: #666 !important;
        margin-top: 20px !important;

        a {
            color: #666;
            text-decoration: none;
            font-weight: 900;
        }
    }
}

// Layout Logo Component.
.jx-setup-wizard-layout-logo {
    .logo-wrapper {
        margin-top: 30px;

        .logo-image {
            max-width: 20%;
            border-radius: 4px;
            border: 1px solid #C9C9C9;
            padding: 10px 15px;
            display:none;

            &.active {
                display: inline-flex;
            }
        }

        .logo-buttons-wrapper {
            display: inline-flex;
            margin-left: 30px;

            .jx-setup-wizard-nav-btn {
                padding: 8px;

                .text img {
                    margin: 0;
                }

                &.logo-delete-button {
                    margin-left: 10px;
                    border-color: #FB9B9F;
                }
            }
        }
    }
}
