@use 'sass:color';
@import '../../styles/bootstrap';

.jx-cp {
    .jx-cp-header {
        .jx-cp-header-top {
            .jx-cp-header-logo-wrapper {
                max-width: 148px;
                height: auto;

                .jx-cp-header-logo-original {
                    max-width: 100%;
                    height: auto;
                }
            }
        }

        .jx-cp-theme-version {
            font-size: 14px;
            padding: 5px 10px;
            font-weight: normal;
            color: #000;
            background: #f5f5f5;
            margin-left: 24px;
        }

        .jx-cp-theme-styles {
            width: 100%;
            max-width: 9.8125rem;
            color: #000;
            border-color: #000;
            background-color: #fff !important;
            box-shadow: none !important;
            border-radius: 2px !important;

            &:hover, &:active, &:focus {
                color: #000 !important;
                border-color: #000 !important;
                background-color: #F2F2F2 !important;
            }

            &.hide-theme-styles {
                display: none;
            }
        }

        .navbar {
            margin-bottom: 35px;
            padding: 24px 0 0;
            font-size: 16px;
            border-bottom: 1px solid $card-border-color;

            .navbar-nav {
                .nav-item {
                    margin-bottom: 0;
                    white-space: pre;
                    position: relative;
                    display: flex;
                    align-items: center;

                    &:hover {
                        .jx-cp-sub-menu-wrap {
                            display: block;
                        }
                    }
                }

                .nav-link {
                    display: flex;
                    align-items: center;
                    padding: 11px 15px;
                    color: #000 !important;
                    border-bottom: 3px solid transparent;
                    line-height: 1.6;
                    font-size: 15px;
                    font-weight: 500;
                    margin: 0 3px;

                    svg {
                        width: 15px;
                        height: 15px;
                    }

                    &:hover {
                        color: #696969 !important;
                        border-bottom-color: transparent;
                    }

                    &:focus,
                    &:active {
                        box-shadow: none;
                        outline: none;
                    }

                    &.nav-active {
                        color: #000 !important;
                        border-bottom-color: #000;
                    }

                    .badge-pill {
                        padding-top: 0;
                        padding-bottom: 0;
                        font-weight: normal;
                        line-height: 1.125rem;
                    }

                    @include media-breakpoint-down(lg) {
                        & {
                            padding-left: 0;
                            padding-right: 0;
                        }
                    }

                    &.help-link {
                        color: #222 !important;

                        &:hover, &:focus, &:active {
                            color: #696969 !important;
                        }
                    }
                }

                //Sub Menu.
                .jx-cp-sub-menu-wrap {
                    display: none;
                    background: #fff;
                    padding: 0;
                    min-width: 210px;
                    border-radius: 4px;
                    box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.15);
                    border: solid 1px #dfdfdf;
                    position: absolute;
                    top: 100%;
                    left: 0;
                    z-index: 99;

                    .dropdown-item {
                        padding: 5px 25px !important;
                        font-size: 14px;
                        font-weight: 500;
                        font-stretch: normal;
                        font-style: normal;
                        line-height: 2.25;
                        letter-spacing: normal;
                        text-align: left;
                        border-radius: 4px;
                        color: #000;
                        box-shadow: none;

                        &:hover {
                            background-color: transparent;
                            color: #696969;
                        }

                        &.active {
                            color: #000;
                            background-color: #F2F2F2;
                        }
                    }
                }
            }

            .jx-cp-help-links-wrap {
                display: flex;
                align-items: center;

                .nav-link {
                    cursor: pointer;
                }
            }

            .navbar-collapse {
                justify-content: space-between;
            }

            @include media-breakpoint-up(xl) {
                .navbar-collapse {
                    max-height: 50px;
                }
            }
        }
    }

    @include media-breakpoint-up(md) {
        .jx-cp-header-top {
            display: flex;
            flex-flow: row wrap;
            align-items: center;
            justify-content: space-between;
        }
    }

    .jx-cp-header-navs {
        white-space: nowrap;
    }

    .jx-cp-updates-counts {
        padding: 2px 7px;
        font-size: 14px;
        font-weight: normal;
        color: #fff;
        background: #fd4b49;
        border-radius: 11px;
    }

    .jx-cp-help {
        svg {
            font-size: 20px;
        }

        span {
            margin-left: 3px;
            vertical-align: middle;
        }
    }

    .jx-cp-header-logo {
        img, svg {
            max-width: 150px;
        }
    }

    @include media-breakpoint-down(sm) {
        .jx-cp-header-logo {
            margin-bottom: 1rem;
        }
    }
}
