.popup-triggers-wrapper {
    margin: 40px 0 60px;

    .mb-3 {
        margin: 0 !important;
    }

    .popup-triggers-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .popup-trigger-row {
        width: 100%;
        max-width: 700px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 10px;
    }

    .popup-trigger-select-wrapper {
        display: flex;
        align-items: center;
        flex-grow: 1;
        margin-right: 10px;
        width: 100%;
        height: 60px;
        max-width: 700px;
        box-shadow: none;
        border: 1px solid #D5D8DC;
        border-radius: 3px;
        background: #ffffff;
    }

    .popup-trigger-name-group,
    .popup-trigger-operator-group {
        position: relative;
        height: 50px;

        .popup-trigger-name,
        .popup-trigger-operator {
            color: #6d7882 !important;
            background-color: #ffffff !important;
            border-radius: 0;
            border-width: 0;
            height: 50px;
            padding: 0;
            appearance: none;
            font-size: 12px;
            cursor: pointer;

            &:hover, &:focus, &:active {
                color: #6d7882 !important;
                background-color: #ffffff !important;
                box-shadow: none;
            }
        }

        .popup-trigger-operator {
            width: 115px !important;
        }

        .popup-trigger-name {
            font-size: 13px;
            width: 180px;
            padding-left: 14px !important;

            option {
                font-size: 12px;
                width: 200px;
            }
        }

        &:after {
            color: #495157;
            content: "\e8ad";
            font-family: eicons;
            font-size: 12px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            pointer-events: none;
        }

        &:not(:first-child) {
            margin-left: 1px !important;
        }
    }

    .popup-trigger-control-group {
        display: flex;
        align-items: center;
        position: relative;
        width: 300px;

        .popup-trigger-control {
            color: #6d7882;
            border-radius: 0;
            border-width: 0;
            padding: 0;
            appearance: none;
            font-size: 12px;
            width: 100%;
            min-height: 27px !important;

            &:focus, &:active, &:hover {
                border-color: #D5D8DC !important;
            }
        }
    }

    .popup-trigger-control-plus-icon {
        display: flex;
        align-items: center;
        justify-content: center;

        span {
            cursor: pointer;
            width: 16.67px;
            position: relative;
            background-color: #f1f3f5;
            line-height: 1.2;
            font-size: 10px;
            border: none;
            border-radius: 0;
            width: 19px;
            height: 19px;

            &::after {
                content: "\e815";
                font-family: eicons;
                color: #515962;
                text-indent: 0;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }

    .popup-trigger-remove-group {
        button {
            color: #515962 !important;
            background-color: transparent !important;
            font-size: 18px;
            cursor: pointer;
            border: none;
            outline: none;
            box-shadow: none;
            margin: 0;
            padding: 0;

            &:hover, &:focus, &:active, &:visited {
                color: #515962 !important;
                background-color: transparent !important;
                box-shadow: none !important;
            }
        }
    }

    #popup-trigger-logic-relation {
        border-bottom: 1px solid black;
        justify-content: space-between;
        padding: 5px;
        color: #515962;
        font-size: 18px;
        select[name="relation_logic"] {
            font-size: 14px;
            height: unset;
        }
    }
}
