.popup-trigger-row {
    .popup-trigger-control {
        &.form-control {
            width: 100%;
            color: #6d7882;
            background: none;
            width: 54px;
            height: auto !important;
            padding: 5px;
            border: 1px solid #D5D8DC !important;
            border-radius: 3px;
            box-shadow: none;
            font-size: 12px;
            line-height: 1;
            font-weight: 400;
            outline: none;
            cursor: text;

            &:focus {
                border-color: #D5D8DC !important;
                box-shadow: none;
            }

            &::selection {
                background-color: #58d0f5;
                color: #6d7882;
            }
        }
    }

    .form-label {
        width: 75px;
        max-width: 60%;
        color: #6d7882;
        margin: 0 5px 0 0;
        padding: 0;
        font-size: 12px;
        font-weight: 400;
        line-height: 1;

        &:focus, &:active {
            color: #10bcf2;

            + input {
                box-shadow: inset 0 0 0 1px #10bcf2;
                border-color: #d5dadf;
            }
        }
    }

    .popup-trigger-type-text {
        input.form-control {
            width: 80px;
            max-width: 100%;
            border-radius: 3px;
            font-size: 12px;
            background: none;
            box-shadow: none;
            color: #6d7882 !important;
            border: 1px solid #d5dadf !important;
            outline: none;
            cursor: text !important;

            &::placeholder {
                color: #515962;
            }

            &:focus {
                border-color: #D5D8DC !important;
            }
        }
    }

    .popup-trigger-type-direction {
        position: relative;

        select {
            width: 80px !important;
            max-width: 100%;
            padding: 7px !important;
            appearance: none;
            cursor: pointer;
        }

        &::after {
            color: #495157;
            content: "\e8ad";
            font-family: eicons;
            font-size: 12px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 5px;
            pointer-events: none;
        }
    }

    .popup-trigger-type-drop-down {
        position: relative;
        width: 300px;

        .popup-trigger-control {
            color: #6d7882 !important;
            border-radius: 3px;
            border-width: 0;
            appearance: none;
            font-size: 12px;
            line-height: normal !important;
            width: 100%;
            min-height: 27px !important;
            border: 1px solid #d5dadf !important;
            cursor: pointer;

            &:focus {
                border-color: #D5D8DC !important;
            }
        }

        &::after {
            color: #495157;
            content: "";
            font-family: eicons;
            font-size: 12px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 5px;
            pointer-events: none;
        }
    }

    .popup-trigger-type-date,
    .popup-trigger-type-date-range {
        width: 200px;
        height: 32px;
        padding: 10px;
        border-radius: 3px;
        border: 1px solid #D5D8DC;
        transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

        &:focus, &:active {
            border-color: #D5D8DC !important;
        }

        &.mb-3 {
            margin: auto 0 !important;
        }

        svg {
            color: #D5D8DC;
            fill: #D5D8DC;
        }

        .react-datepicker-wrapper {
            width: 100% !important;
        }

        &.jupiterx-popup-date-input-open {
            border-color: #D5D8DC !important;
        }

        .jupiterx-popup-date-input {
            color: #6d7882;
            background-color: transparent;
            border-width: 0;
            font-size: 12px;
            width: 100%;
        }
    }

    .popup-trigger-type-time-range {
        .form-label {
            width: auto !important;
        }

        input, select {
            width: 95px;
            color: #6d7882 !important;
            border: 1px solid #d5dadf !important;
            outline: none;
            font-size: 12px;
            box-shadow: none !important;
            cursor: text;

            &::placeholder {
                color: #D5D8DC;
            }

            &:focus {
                border-color: #D5D8DC !important;
            }
        }

        .jupiterx-popup-time-range-timezone-wrapper {
            position: relative;
            width: 115px;

            select {
                width: 120px !important;
                appearance: none;
                cursor: pointer;
            }

            &:after {
                color: #495157;
                content: "\e8ad";
                font-family: eicons;
                font-size: 12px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 0;
                pointer-events: none;
            }
        }
    }

    .popup-trigger-url_referrer {
        .popup-trigger-control {
            width: 200px !important;
        }
    }
}
