.jx-cp-logs-messages {
    background: #dfdfdf;
    max-height: 700px;
    overflow: auto;
    border-radius: 3px;

    details,
    div {
        line-height: 2;
        white-space: nowrap;
    }

    pre {
        background-color: #c3c3c3;
        padding: 20px;
    }
}

button svg {
    margin: 0 3px 2px 0;
}

.jx-cp-logs-actions {
    display: flex;
    align-items: center;

    button {
        background-color: #000 !important;
        color: #fff !important;
        border-color: #000 !important;
        box-shadow: none !important;
        white-space: nowrap;
        border-radius: 2px !important;

        &:disabled, &:hover, &:focus, &:active {
            color: #fff !important;
            background-color: #696969 !important;
            border-color: #696969 !important;
        }
    }
}
